import { Component, OnInit } from '@angular/core';
import { Impuesto } from 'src/app/models/impuesto.model';
import Swal from 'sweetalert2';
import { ImpuestosService } from '../../services/impuestos.service';

@Component({
  selector: 'app-impuestos',
  templateUrl: './impuestos.component.html',
  styles: [
  ]
})
export class ImpuestosComponent implements OnInit {

  constructor(private impuestoServices: ImpuestosService) { }

  public impuestos: Impuesto[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");


  ngOnInit(): void {
    console.log("Iniciando");
    this.CargarImpuestos();
  }

  CargarImpuestos(){
    //console.log("object2");
    this.cargando = true;
    this.impuestoServices.DevolverImpuestos(this.empresa).subscribe( ({parimpuesto}) => {
      this.impuestos = parimpuesto;
      this.cargando = false;
      console.log(parimpuesto);
    });
  }

  Borrar(parimpuesto: Impuesto){
    Swal.fire({
      title: 'Esta seguro de borrar el impuesto?',
      text: `Impuesto: ${parimpuesto.idimpuesto}`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then(result => {
      if(result.value){
              this.impuestoServices.BorrarImpuesto(parimpuesto.idimpuesto).subscribe(resp => {
                this.CargarImpuestos();
                Swal.fire('Impuesto Borrado',`${parimpuesto.idimpuesto} fue eliminado correctamente`,'success');
              });
      }
    })
  }

}
