import { Injectable } from '@angular/core';
import { cabcobro } from '../models/cabcobro.model';
import { detcobro } from '../models/detcobro.model';
import { HttpClient } from '@angular/common/http';
import { GLOBAL } from './global';
import { CargarCobroCabeceras, IngresarCobroCabeceras, IngresarDetallesCobros } from '../interfaces/cargar.interface';

@Injectable({
  providedIn: 'root'
})
export class CobrosService {

  public url: string;

  constructor(private http: HttpClient) { 
    this.url = GLOBAL.url;
  }

  CrearCabeceraCobro( cobro: cabcobro ){
    const urlService = `${this.url}cabcobro`;
    return this.http.post<IngresarCobroCabeceras>(urlService,cobro,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  CrearDetalleCobro( detallecobro: detcobro){
    const urlService = `${this.url}detcobro`;
    return this.http.post<IngresarDetallesCobros>(urlService, detallecobro, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Servicio para devolver la cabecera de cobros 
  DevolverCabcobros(idEmpresa){
    const urlService = `${this.url}cabcobros/${idEmpresa}`;
    return this.http.get<CargarCobroCabeceras>(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  

}
