import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GLOBAL } from './global';

@Injectable({
  providedIn: 'root'
})
export class ReportesFacturasService {

  public url: string;

  constructor(private http: HttpClient) { 
    this.url = GLOBAL.url;
  }

  ReporteFacturasFechas(empresa: string, fechaini: Date, fechafin: Date, idcaja: string){
    console.log(empresa,fechaini,fechafin);
    const urlService = `${this.url}cabmovfacontado/${empresa}/${fechaini}/${fechafin}/${idcaja}`;
    return this.http.get(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  ReporteCuentasCobrar(empresa: string, fechaini: Date, fechafin: Date, idcaja: string
    ,idestado:string){
    console.log(empresa,fechaini,fechafin);
    const urlService = `${this.url}cuentasporcobrar/${empresa}/${fechaini}/${fechafin}/${idcaja}/${idestado}`;
    return this.http.get(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  ReporteFacturasporCobrar(empresa: string, idRucCi: string){
    const urlService = `${this.url}facturasporcobrar/${empresa}/${idRucCi}`;
    return this.http.get(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }



}
