import { Component, OnInit } from '@angular/core';
import { Caja } from 'src/app/models/caja.model';
import Swal from 'sweetalert2';
import { CajaService } from '../../services/caja.service';

@Component({
  selector: 'app-caja',
  templateUrl: './caja.component.html',
  styles: [
  ]
})
export class CajaComponent implements OnInit {

  constructor(private cajaServices: CajaService) { }

  public cajas: Caja[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");

  ngOnInit(): void {
    console.log("Iniciando");
    this.CargarCajas();
  }

  CargarCajas(){
    //console.log("object2");
    this.cargando = true;
    this.cajaServices.DevolverCajas(this.empresa).subscribe( ({parcaja}) => {
      this.cajas = parcaja;
      this.cargando = false;
      console.log(parcaja);
    });
  }

  Borrar(parcaja: Caja){
    Swal.fire({
      title: 'Esta seguro de borrar la caja?',
      text: `Caja: ${parcaja.idcaja}`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then(result => {
      if(result.value){
              this.cajaServices.BorrarCaja(parcaja.idcaja).subscribe(resp => {
                this.CargarCajas();
                Swal.fire('Caja Borrada',`${parcaja.idcaja} fue eliminado correctamente`,'success');
              });
      }
    })
  }

}
