import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Caja } from 'src/app/models/caja.model';
import { Parserie } from 'src/app/models/parserie.model';
import { Usuarios } from 'src/app/models/usuariom.model';
import { CajaService } from 'src/app/services/caja.service';
import { ParserieService } from 'src/app/services/parserie.service';
import { UsuariosService } from 'src/app/services/usuarios.service';
import { UsucajaserieService } from 'src/app/services/usucajaserie.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-actualizar-usucajaserie',
  templateUrl: './actualizar-usucajaserie.component.html',
  styles: [
  ]
})
export class ActualizarUsucajaserieComponent implements OnInit {

  public usucajaserieForm: FormGroup;
  public usucajaserieSubido: FormGroup;
  
  public cajas: Caja[] = [];
  public series: Parserie[] = [];
  private codigoSectabla: number = 0;
  public users: Usuarios[] = [];

  constructor(private fb: FormBuilder,
    private cajaService: CajaService,
    private parserieServices: ParserieService,
    private usucajaserieService: UsucajaserieService,
    private usuariosService: UsuariosService,
    private router: Router,
    private activatedRoute: ActivatedRoute) 
    {
      this.CargarCajas();
      this.CargarSeries();
      this.CargarUsuarios();
     }

  ngOnInit(){ this.crearFormulario();
  this.cargarUsucajaserie();}

  CargarUsuarios(){
    this.usuariosService.devolverUsuarios(this.empresa).subscribe(({usuarios})=>{
      this.users = usuarios;
    });
  }

  CargarCajas(){
    this.cajaService.DevolverCajas(this.empresa).subscribe(({parcaja})=>{
      this.cajas = parcaja;
    });
  }

  CargarSeries(){
    this.parserieServices.DevolverSeries(this.empresa).subscribe( ({parserie}) => {
    this.series = parserie;
    });
  }


  //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
  empresa: string = localStorage.getItem('idemp');

  crearFormulario = () => {
    this.usucajaserieForm = this.fb.group({
      idempresa: [this.empresa],
      sectabla:[0],
      idsucursal: ['SUC01'],
      idusuario: [localStorage.getItem('idcli')],
      idcaja: ['', Validators.required],
      idserie: ['', Validators.required],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      activo: [true],
    });

  }

  cargarUsucajaserie() {
    this.activatedRoute.params.subscribe(({ id }) => {
      this.codigoSectabla = id;
      console.log(this.codigoSectabla);
    });
    this.usucajaserieService.cargarUsucajaserie(this.codigoSectabla).subscribe((resp) => {
      console.log(resp);
      const {
        idempresa,
        idusuario,
        sectabla,
        idsucursal,
        idcaja,
        idserie,
        crepor,
        activo,
      } = resp;
      const modpor = [localStorage.getItem('idcli')];
      this.usucajaserieForm.setValue({
        idempresa,
        idusuario,
        sectabla,
        idsucursal,
        idcaja,
        idserie,
        crepor,
        modpor,
        activo,
      });
      return resp;
    });
  }


  actualizarUsucajaserie() {
    console.log('Guardar');
    this.usucajaserieSubido = this.usucajaserieForm.value;
    const datos = {
      ...this.usucajaserieForm.value
    } 
    console.log(datos.sectabla);
    this.usucajaserieService
      .actualizarUsucajaserie(
        datos.sectabla,
        datos
      )
      .subscribe(
        (resp) => {
          console.log(resp);
          Swal.fire('Actualizado ', this.usucajaserieForm.value['sectabla'] + ' Usuario - Caja - Serie actualizada correctamente', 'success');
          this.router.navigateByUrl('dashboard/usucajaserie');
        },
        (err) => {
          Swal.fire(
            'Error',
            'No se ha podido actualizar el Usuario - Caja - Serie intente mas tarde',
            'error'
          );
          console.log(err);
        }
      );
  }

  
  regresar() {
    this.router.navigateByUrl('dashboard/usucajaserie');
  }

}
