import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Precioclientes } from '../models/precioclientes.model';
import { GLOBAL } from './global';
import { CargarPrecioClientes } from '../interfaces/cargar.interface';


@Injectable({
  providedIn: 'root'
})
export class PrecioclientesService {

  constructor(private http: HttpClient) { 
    this.url=GLOBAL.url;
  }

  public precioclientes: Precioclientes[] = [];

  private url: string;

  //Servicio para devolver impuestos especifico
  DevolverPrecioClientes(idEmpresa){
    const urlService = `${this.url}precioclientesall/${idEmpresa}`;
    return this.http.get<CargarPrecioClientes>(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  BorrarPrecioClientes(idSucursal: number){
    const urlService = `${this.url}precioclientes/delete/${idSucursal}`; 
    return this.http.delete(urlService,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Servicio para devolver impuestos especifico
  DevolverPrecioEspecial(idEmpresa,idCliente,idProducto){
    const urlService = `${this.url}preciocliente/${idEmpresa}/${idCliente}/${idProducto}`;
    return this.http.get<CargarPrecioClientes>(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

}
