import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Sucursal } from 'src/app/models/sucursal.model';
import { SucursalService } from 'src/app/services/sucursal.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-actualizar-sucursal',
  templateUrl: './actualizar-sucursal.component.html',
  styles: [
  ]
})
export class ActualizarSucursalComponent implements OnInit {

  public sucursalForm: FormGroup;
  private codigoSucursal: string = '';
  public sucursal: Sucursal;
  public sucursalSubido: FormGroup;



  constructor(private fb: FormBuilder,
  private sucursalService: SucursalService,
  private router: Router,
  private activatedRoute: ActivatedRoute) 
  { 

  }

  ngOnInit(){ 
    this.crearFormulario();
    this.CargarSucursal();
  }


  //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
  empresa: string = localStorage.getItem('idemp');

  crearFormulario = () => {
    this.sucursalForm = this.fb.group({
      idempresa: [this.empresa],
      idsucursal: ['', Validators.required],
      nomsucursal: ['', Validators.required],
      direccion: ['', Validators.required],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      activo: [true],
    });

  }

  CargarSucursal() {
    this.activatedRoute.params.subscribe(({ id }) => {
      this.codigoSucursal = id;
      console.log(this.codigoSucursal);
    });
    this.sucursalService.cargarSucursal(this.codigoSucursal).subscribe((resp) => {
      console.log(resp);
      const {
        idempresa,
        idsucursal,
        nomsucursal,
        direccion,
        activo,
        crepor
      } = resp;
      const modpor = 1;
      this.sucursal = resp;
      this.sucursalForm.setValue({
        idempresa,
        idsucursal,
        nomsucursal,
        direccion,
        crepor,
        modpor,
        activo,
      });
      return resp;
    });
  }

  actualizarSucursal() {
    console.log('Guardar');
    this.sucursalSubido = this.sucursalForm.value;
    const datos = {
      ...this.sucursalForm.value
    } 
    console.log(datos);
    this.sucursalService
      .actualizarSucursal(
        datos.idsucursal,
        datos
      )
      .subscribe(
        (resp) => {
          console.log(resp);
          Swal.fire('Actualizado ', this.sucursalForm.value['idsucursal'] + ' sucursal actualizada correctamente', 'success');
          this.router.navigateByUrl('dashboard/sucursales');
        },
        (err) => {
          Swal.fire(
            'Error',
            'No se ha podido actualizar la sucursal intente mas tarde',
            'error'
          );
          console.log(err);
        }
      );
  }




  regresar() {
    this.router.navigateByUrl('dashboard/sucursales');
  }


}
