import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { PagesComponent } from './pages.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ClientesComponent } from './clientes/clientes.component';
import { NuevoComponent } from './clientes/nuevo/nuevo.component';
import { ClienteComponent } from './clientes/cliente/cliente.component';
import { AuthGuard } from '../guards/auth.guard';
import { FormasPagoComponent } from './formas-pago/formas-pago.component';
import { NuevoFormasPagoComponent } from './formas-pago/nuevo/nuevo.component';
import { ActualizarFormaPagoComponent } from './formas-pago/actualizar/actualizar.component';
import { UnidadComponent } from './unidad/unidad.component';
import { CrearProformasComponent } from './proformas/crear-proformas/crear-proformas.component';
import { CrearFacturasComponent } from './facturas/crear-facturas/crear-facturas.component';
import { ProductosComponent } from './productos/productos.component';
import { ListaproformasComponent } from './proformas/listaproformas/listaproformas.component';
import { ListafacturasComponent } from './facturas/listafacturas/listafacturas.component';
import { AdminGuard } from '../guards/admin.guard';
import { ActualizarProductosComponent } from './productos/actualizar-productos/actualizar-productos.component';
import { NuevoProductosComponent } from './productos/nuevo-productos/nuevo-productos.component';
import { ImpuestosComponent } from './impuestos/impuestos.component';
import { SucursalComponent } from './sucursal/sucursal.component';
import { NuevoUnidadComponent } from './unidad/nuevo-unidad/nuevo-unidad.component';
import { ActualizarUnidadComponent } from './unidad/actualizar-unidad/actualizar-unidad.component';
import { ActualizarImpuestosComponent } from './impuestos/actualizar-impuestos/actualizar-impuestos.component';
import { NuevoImpuestosComponent } from './impuestos/nuevo-impuestos/nuevo-impuestos.component';
import { ActualizarSucursalComponent } from './sucursal/actualizar-sucursal/actualizar-sucursal.component';
import { NuevoSucursalComponent } from './sucursal/nuevo-sucursal/nuevo-sucursal.component';
import { FamiliasComponent } from './familias/familias.component';
import { NuevoFamiliaComponent } from './familias/nuevo-familia/nuevo-familia.component';
import { ActualizarFamiliaComponent } from './familias/actualizar-familia/actualizar-familia.component';
import { UsuariosComponent } from './usuarios/usuarios.component';
import { ActualizarUsuariosComponent } from './usuarios/actualizar-usuarios/actualizar-usuarios.component';
import { NuevoUsuariosComponent } from './usuarios/nuevo-usuarios/nuevo-usuarios.component';
import { SerieComponent } from './serie/serie.component';
import { ActualizarSerieComponent } from './serie/actualizar-serie/actualizar-serie.component';
import { NuevoSerieComponent } from './serie/nuevo-serie/nuevo-serie.component';
import { CajaComponent } from './caja/caja.component';
import { ActualizarCajaComponent } from './caja/actualizar-caja/actualizar-caja.component';
import { NuevoCajaComponent } from './caja/nuevo-caja/nuevo-caja.component';
import { UsucajaserieComponent } from './usucajaserie/usucajaserie.component';
import { ActualizarUsucajaserieComponent } from './usucajaserie/actualizar-usucajaserie/actualizar-usucajaserie.component';
import { NuevoUsucajaserieComponent } from './usucajaserie/nuevo-usucajaserie/nuevo-usucajaserie.component';
import { EmpresaComponent } from './empresa/empresa.component';
import { PrecioclientesComponent } from './precioclientes/precioclientes.component';
import { ActualizarEmpresaComponent } from './empresa/actualizar-empresa/actualizar-empresa.component';
import { NuevoEmpresaComponent } from './empresa/nuevo-empresa/nuevo-empresa.component';
import { ActualizarPrecioclientesComponent } from './precioclientes/actualizar-precioclientes/actualizar-precioclientes.component';
import { NuevoPrecioclientesComponent } from './precioclientes/nuevo-precioclientes/nuevo-precioclientes.component';
import { EstrucuentasComponent } from './estrucuentas/estrucuentas.component';
import { ActualizarEstrucuentasComponent } from './estrucuentas/actualizar-estrucuentas/actualizar-estrucuentas.component';
import { NuevoEstrucuentasComponent } from './estrucuentas/nuevo-estrucuentas/nuevo-estrucuentas.component';
import { ContplancuentasComponent } from './contplancuentas/contplancuentas.component';
import { ActualizarContplancuentasComponent } from './contplancuentas/actualizar-contplancuentas/actualizar-contplancuentas.component';
import { NuevoContplancuentasComponent } from './contplancuentas/nuevo-contplancuentas/nuevo-contplancuentas.component';
import { CrearFacturasParametroComponent } from './facturas/crear-facturas-parametro/crear-facturas-parametro.component';
import { CobrosComponent } from './cobros/cobros.component';
import { FacturasComponent } from './reportes/facturas/facturas.component';
import { ProformasComponent } from './reportes/proformas/proformas.component';
import { CuentascobrarComponent } from './reportes/cuentascobrar/cuentascobrar.component';
import { CobrosfacturasComponent } from './cobros/cobrosfacturas/cobrosfacturas.component';

const routes: Routes = [
  {
    path: 'dashboard',
    component: PagesComponent,
    canActivate: [AuthGuard],
    children: [
      { path: '', canActivate:[AuthGuard],component: DashboardComponent },
      { path: 'facturar', canActivate:[AuthGuard],component: CrearFacturasComponent },
      { path: 'facturar/:id', canActivate:[AuthGuard],component: CrearFacturasParametroComponent },
      { path: 'listafacturas', canActivate:[AuthGuard],component: ListafacturasComponent },
      { path: 'proformar', canActivate:[AuthGuard],component: CrearProformasComponent },
      { path: 'listaproformas', canActivate:[AuthGuard],component: ListaproformasComponent },
      { path: 'clientes', canActivate:[AuthGuard],component: ClientesComponent },
      { path: 'clientes/nuevo', canActivate:[AuthGuard],component: NuevoComponent },
      { path: 'cliente/:id', canActivate:[AuthGuard],component: ClienteComponent },
      
      //Rutas de Admin
      { path: 'formasPago', canActivate:[AdminGuard, AuthGuard], component: FormasPagoComponent },
      { path: 'formasPago/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoFormasPagoComponent },
      { path: 'formasPago/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarFormaPagoComponent },
      { path: 'unidades', canActivate:[AdminGuard, AuthGuard], component: UnidadComponent },
      { path: 'unidades/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoUnidadComponent },
      { path: 'unidades/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarUnidadComponent },
      { path: 'productos', canActivate:[AdminGuard, AuthGuard], component: ProductosComponent },
      { path: 'productos/:id', canActivate:[AdminGuard, AuthGuard], component:  ActualizarProductosComponent},
      { path: 'producto/nuevo', canActivate:[AdminGuard, AuthGuard], component:  NuevoProductosComponent},
      { path: 'impuesto', canActivate:[AdminGuard, AuthGuard], component: ImpuestosComponent },
      { path: 'impuesto/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarImpuestosComponent },
      { path: 'impuestos/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoImpuestosComponent },
      { path: 'sucursales', canActivate:[AdminGuard, AuthGuard], component: SucursalComponent }, 
      { path: 'sucursales/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarSucursalComponent },
      { path: 'sucursal/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoSucursalComponent },
      { path: 'familia', canActivate:[AdminGuard, AuthGuard], component: FamiliasComponent }, 
      { path: 'familia/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarFamiliaComponent },
      { path: 'familias/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoFamiliaComponent },
      { path: 'usuarios', canActivate:[AdminGuard, AuthGuard], component: UsuariosComponent }, 
      { path: 'usuarios/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarUsuariosComponent },
      { path: 'usuariosnew/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoUsuariosComponent },
      { path: 'serie', canActivate:[AdminGuard, AuthGuard], component: SerieComponent }, 
      { path: 'serie/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarSerieComponent },
      { path: 'series/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoSerieComponent },
      { path: 'caja', canActivate:[AdminGuard, AuthGuard], component: CajaComponent }, 
      { path: 'caja/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarCajaComponent },
      { path: 'cajas/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoCajaComponent },
      { path: 'usucajaserie', canActivate:[AdminGuard, AuthGuard], component: UsucajaserieComponent }, 
      { path: 'usucajaserie/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarUsucajaserieComponent },
      { path: 'usucajaseries/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoUsucajaserieComponent },
      { path: 'empresa', canActivate:[AdminGuard, AuthGuard], component: EmpresaComponent }, 
      { path: 'empresa/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarEmpresaComponent },
      { path: 'empresas/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoEmpresaComponent },
      { path: 'precioclientes', canActivate:[AdminGuard, AuthGuard], component: PrecioclientesComponent }, 
      { path: 'precioclientes/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarPrecioclientesComponent },
      { path: 'precioclientesn/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoPrecioclientesComponent },
      { path: 'estrucuentas', canActivate:[AdminGuard, AuthGuard], component: EstrucuentasComponent }, 
      { path: 'estrucuentas/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarEstrucuentasComponent },
      { path: 'estrucuentasn/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoEstrucuentasComponent },
      { path: 'contplancuentas', canActivate:[AdminGuard, AuthGuard], component: ContplancuentasComponent }, 
      { path: 'contplancuentas/:id', canActivate:[AdminGuard, AuthGuard], component: ActualizarContplancuentasComponent },
      { path: 'contplancuentasn/nuevo', canActivate:[AdminGuard, AuthGuard], component: NuevoContplancuentasComponent },
      { path: 'cobros', canActivate:[AdminGuard, AuthGuard], component: CobrosComponent }, 
      { path: 'reportesFacturas', canActivate:[AdminGuard, AuthGuard], component: FacturasComponent }, 
      { path: 'reportesProformas', canActivate:[AdminGuard, AuthGuard], component: ProformasComponent },
      { path: 'reportesCuentasCobrar', canActivate:[AdminGuard, AuthGuard], component: CuentascobrarComponent },  
      { path: 'cobrosfacturas', canActivate:[AdminGuard, AuthGuard], component: CobrosfacturasComponent },  
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PagesRoutingModule {}
