import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { EmpresaService } from 'src/app/services/empresa.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-nuevo-empresa',
  templateUrl: './nuevo-empresa.component.html',
  styles: [
  ]
})
export class NuevoEmpresaComponent implements OnInit {

  public empresaForm: FormGroup;

  constructor(private fb: FormBuilder,
    private empresaService: EmpresaService,
    private router: Router) { }

  ngOnInit(){ this.crearFormulario();}


  crearFormulario = () => {
    this.empresaForm = this.fb.group({
      idempresa: ['', Validators.required],
      nomempresa: ['', Validators.required],
      rucciempresa: ['', Validators.required],
      paisempresa: ['', Validators.required],
      dirempresa: ['', Validators.required],
      logoempresa: [''],
      monempresa: [''],
      decempresa: [0],
      telf1empresa: [''],
      telf2empresa: [''],
      email1empresa: [''],
      email2empresa: [''],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      nomcomercial: ['', Validators.required],
      activo: [true], 
      razonsocial: ['', Validators.required], 
      contabilidad: [false],  
      ambiente: ['Pruebas'],
      wsdl1: [''],
      wsdl2: [''],
      clientessuc: [false],
      productosssuc: [false],
      modeloFE: ['Off-Line'],
      });

  }

  guardarEmpresa() {
    this.empresaService.crearEmpresa(this.empresaForm.value).subscribe(resp => {
      console.log(resp);
      Swal.fire('Creado','Nueva empresa creada correctamente','success');
      this.router.navigateByUrl('dashboard/empresa');
    }, (err) => {Swal.fire('Error','No se ha podido crear la empresa intente mas tarde','error');});
  }

  regresar() {
    this.router.navigateByUrl('dashboard/empresa');
  }


}
