import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Caja } from 'src/app/models/caja.model';
import { CajaService } from 'src/app/services/caja.service';
import { ReportesFacturasService } from '../../../services/reportes-facturas.service';
import { ExporterService } from '../../../services/exporter.service';


@Component({
  selector: 'app-cuentascobrar',
  templateUrl: './cuentascobrar.component.html',
  styleUrls: ['./cuentascobrar.component.css']
})
export class CuentascobrarComponent implements OnInit {

  empresa: string = localStorage.getItem("idemp");
  fechaini = '';
  fechafin = '';
  idcaja = '';
  idestado = '';
  subtotal: number = 0.00;
  subtotaliva0: number = 0.00;
  iva0: number = 0.00; 
  subtotaliva12: number = 0.00;
  iva12: number = 0.00; 
  total: number = 0.00;
  valorcobrado: number = 0.00;


  public cajas: Caja[] = [];
  
  
  constructor(private reporteFacturasService: ReportesFacturasService,
    private excel: ExporterService,
    private fb: FormBuilder,
    private cajaService: CajaService,) { 
      this.CargarCajas();
    }

  public facturas:any = [];

  ngOnInit(): void {
    
  }

  CargarCajas(){
    this.cajaService.DevolverCajas(this.empresa).subscribe(({parcaja})=>{
      this.cajas = parcaja;
    });
  }

  CargarReporteCuentasCobrar(fechaini,fechafin,idcaja,idestado){
    this.subtotal = 0.00;  
    this.subtotaliva0 = 0.00;
    this.iva0 = 0.00;
    this.subtotaliva12 = 0.00;  
    this.iva12 = 0.00;  
    this.total = 0.00;  
    this.valorcobrado = 0.00;  
    this.reporteFacturasService.ReporteCuentasCobrar(this.empresa,fechaini,fechafin,idcaja,idestado).subscribe(resp=>{
      this.facturas = resp;
      for (let index = 0; index < this.facturas.length; index++){
        const element = this.facturas[index];
        this.subtotal = this.subtotal + Number(element.subtotal);
        this.subtotaliva0 = this.subtotaliva0 + Number(element.subtotaliva0);
        this.iva0 = this.iva0 + Number(element.iva0);
        this.subtotaliva12 = this.subtotaliva12 + Number(element.subtotaliva12);
        this.iva12 = this.iva12 + Number(element.iva12);
        this.total = this.total + Number(element.total);  
        this.valorcobrado = this.valorcobrado + Number(element.valorcobro);          
      }
    })
  }

  ExportarXLSX(fechaini,fechafin,idcaja,idestado){
    /* this.subtotal = 0.00;  
    this.subtotaliva0 = 0.00;
    this.iva0 = 0.00;
    this.subtotaliva12 = 0.00;  
    this.iva12 = 0.00;  
    this.total = 0.00;   */
    this.reporteFacturasService.ReporteCuentasCobrar(this.empresa,fechaini,fechafin,idcaja,idestado).subscribe(resp=>{
      this.facturas = resp;
      this.excel.exporToExcel(this.facturas,'ReporteCuentasCobrar')
      /* for (let index = 0; index < this.facturas.length; index++){
        const element = this.facturas[index];
        this.subtotal = this.subtotal + Number(element.subtotal);
        this.subtotaliva0 = this.subtotaliva0 + Number(element.subtotaliva0);
        this.iva0 = this.iva0 + Number(element.iva0);
        this.subtotaliva12 = this.subtotaliva12 + Number(element.subtotaliva12);
        this.iva12 = this.iva12 + Number(element.iva12);
        this.total = this.total + Number(element.total);  
      } */
    })
  }

}
