import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GLOBAL } from './global';
import { CargarDetallesCobros } from '../interfaces/cargar.interface';



@Injectable({
  providedIn: 'root'
})
export class DetallecobrosService {

  public url: string;

  constructor(private http: HttpClient) { 
    this.url = GLOBAL.url;
  }

  //Servicio para devolver el detalle de cobros 
  DevolverDetcobros(idCobro,idEmpresa){
    const urlService = `${this.url}detcobro/${idCobro}/${idEmpresa}`;
    return this.http.get<CargarDetallesCobros>(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }
}
