import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filter'
})
export class FilterPipe implements PipeTransform {

  transform(value: any, arg1: any, arg2: any, arg3: any, arg4: any, arg5: any, arg6: any): any {
    const resultClientes = [];
    for(const cliente of value){
      if((cliente.idcliente.toLowerCase().indexOf(arg1.toLowerCase())>-1) && (cliente.ruccicliente.toLowerCase().indexOf(arg2.toLowerCase())>-1)&&(cliente.nomcliente.toLowerCase().indexOf(arg3.toLowerCase())>-1) && (cliente.dircliente.toLowerCase().indexOf(arg4.toLowerCase())>-1)&&(cliente.telfcliente.toLowerCase().indexOf(arg5.toLowerCase())>-1) && (cliente.emailcliente.toLowerCase().indexOf(arg6.toLowerCase())>-1)){
        resultClientes.push(cliente);
      }
    }
    return resultClientes;
  }

}
