import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterfacturas'
})
export class FilterfacturasPipe implements PipeTransform {

  transform(value: any, arg1: any, arg2: any, arg3: any, arg4: any, arg5: any, arg6: any): any {
    const resultFacturas = [];
    for (const factura of value) {
      try {
        //Aumentar las condiciones && (factura.EstadoAutorizacionSRI.toLowerCase().indexOf(arg5.toLowerCase()) > -1) && (factura.EstadoRecepcionSRI.toLowerCase().indexOf(arg6.toLowerCase()) > -1) cuando se solucione el error de las facturas que no guardan estado
        if ((factura.secmovcab.indexOf(arg1) > -1) && (factura.idcliente.toLowerCase().indexOf(arg2.toLowerCase()) > -1) && (factura.numfactura.toLowerCase().indexOf(arg3.toLowerCase()) > -1) && (factura.numautosri.toLowerCase().indexOf(arg4.toLowerCase()) > -1)) {
          resultFacturas.push(factura);
        }
      } catch (error) {
        console.log(error);
      }

    }
    return resultFacturas;
  }

}
