import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { CajaService } from 'src/app/services/caja.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-nuevo-caja',
  templateUrl: './nuevo-caja.component.html',
  styles: [
  ]
})
export class NuevoCajaComponent implements OnInit {

  public cajaForm: FormGroup;
  constructor(private fb: FormBuilder,
    private cajaService: CajaService,
    private router: Router) { }

  ngOnInit(){ this.crearFormulario();}


  //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
  empresa: string = localStorage.getItem('idemp');

  crearFormulario = () => {
    this.cajaForm = this.fb.group({
      idempresa: [this.empresa],
      idsucursal: ['SUC01'],
      idcaja: ['', Validators.required],
      nomcaja: ['', Validators.required],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      activo: [true],
    });

  }

  guardarCaja() {
    this.cajaService.crearCaja(this.cajaForm.value).subscribe(resp => {
      console.log(resp);
      Swal.fire('Creado','Nueva caja creada correctamente','success');
      this.router.navigateByUrl('dashboard/caja');
    }, (err) => {Swal.fire('Error','No se ha podido ingresar la caja intente mas tarde','error');});
  }

  regresar() {
    this.router.navigateByUrl('dashboard/caja');
  }


}
