import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterproductos'
})
export class FilterproductosPipe implements PipeTransform {

  transform(value: any, arg1: any, arg2: any, arg3: any, arg4: any, arg5: any, arg6: any, arg7): any {
    const productos = [];
    for (const producto of value) {
      if ((producto.idproducto.toLowerCase().indexOf(arg1.toLowerCase()) > -1)&&
      (producto.nomproducto.toLowerCase().indexOf(arg2.toLowerCase()) > -1) &&
      (producto.precioprodu.toLowerCase().indexOf(arg3.toLowerCase()) > -1) &&
      (producto.idfamilia.toLowerCase().indexOf(arg4.toLowerCase()) > -1) &&
      (producto.idunidad.toLowerCase().indexOf(arg5.toLowerCase()) > -1) &&
      (producto.tipproducto.toLowerCase().indexOf(arg6.toLowerCase()) > -1) &&
      (producto.idimpuesto.toLowerCase().indexOf(arg7.toLowerCase()) > -1)) {
        productos.push(producto);
      }
    }
    return productos;
  }

}
