import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Usuarios } from 'src/app/models/usuariom.model';
import Swal from 'sweetalert2';
import { UsuariosService } from '../../services/usuarios.service';


@Component({
  selector: 'app-usuarios',
  templateUrl: './usuarios.component.html',
  styles: [
  ]
})
export class UsuariosComponent implements OnInit {

  public users: Usuarios[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");

  constructor(private usuariosServices: UsuariosService,
    private router: Router) { }

  ngOnInit(): void {
    console.log("Iniciando");
    this.devolverUsuarios();
  }

  devolverUsuarios(){
    //console.log("object2");
    this.cargando = true;
    this.usuariosServices.devolverUsuarios(this.empresa).subscribe( ({usuarios}) => {
      this.users = usuarios;
      this.cargando = false;
      console.log(usuarios);
    });
  }

  Borrar(usuarios: Usuarios){
    Swal.fire({
      title: 'Esta seguro de borrar el usuario?',
      text: `Unidad: ${usuarios.idusuario}`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then(result => {
      if(result.value){
              this.usuariosServices.borrarUsuarios(usuarios.idusuario).subscribe(resp => {
                this.devolverUsuarios();
                Swal.fire('Usuario Borrado',`${usuarios.idusuario} fue eliminado correctamente`,'success');
              });
      }
    })
  }

}
