import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ClientesService } from '../../services/clientes.service';
import { Cliente } from "../../models/cliente.model";
import Swal from 'sweetalert2';
import { Router } from '@angular/router';
declare function customInitFunctions();
declare function sidebar2();
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { DataSource } from '@angular/cdk/collections';
import { Observable, ReplaySubject } from 'rxjs';

@Component({
  selector: 'app-clientes',
  templateUrl: './clientes.component.html',
  styleUrls: [
    './clientes.component.css'
  ]
})
export class ClientesComponent implements OnInit{
  
  public totalClientes: number = 0;
  public clientes: Cliente[] = [];
  public cargando: boolean = true;

  constructor( private clienteService: ClientesService,
    private router: Router) { }

    filterClientesNombre = '';
    filterClientesCodigo = '';
    filterClientesRuc = '';
    filterClientesDireccion = '';
    filterClientesTelefono = '';
    filterClientesEmail = '';

    empresa: string = localStorage.getItem("idemp");

  @ViewChild(MatPaginator) paginator: MatPaginator;

  pageActual: number = 1;

  ngOnInit(): void {
    this.CargarCliente();
  }

  CargarCliente(){
    this.cargando = true;
    this.clienteService.cargarClientes(this.empresa).subscribe( ({clientes}) => {
      this.clientes = clientes;
      this.cargando = false;
      console.log(clientes);
    });
  }

  Borrar(cliente:Cliente){
    if(cliente.ruccicliente != '9999999999999'){
      Swal.fire({
        title: 'Esta seguro de borrar el cliente?',
        text: `Cliente: ${cliente.nomcliente}`,
        icon: 'error',
        showCancelButton: true,
        confirmButtonText: 'Si, borrarlo'
      }).then(result => {
        if(result.value){
          this.clienteService.borrarClientes(cliente.idcliente).subscribe(resp => {
            this.CargarCliente();
            Swal.fire('Cliente borrado',`${cliente.nomcliente} fue eliminado correctamente`,'success');
          })
        }
      })
    }else{
      Swal.fire({
        title: 'Error',
        text: `El cliente consumidor final no se puede borrar`,
        icon: 'error',
      })
    }
  }
}
