import { Component, OnInit } from '@angular/core';
import { Parserie } from 'src/app/models/parserie.model';
import Swal from 'sweetalert2';
import { ParserieService } from '../../services/parserie.service';

@Component({
  selector: 'app-serie',
  templateUrl: './serie.component.html',
  styles: [
  ]
})
export class SerieComponent implements OnInit {

  constructor(private parserieServices: ParserieService) { }

  public series: Parserie[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");


  ngOnInit(): void {
    console.log("Iniciando");
    this.CargarSeries();
  }

  CargarSeries(){
    //console.log("object2");
    this.cargando = true;
    this.parserieServices.DevolverSeries(this.empresa).subscribe( ({parserie}) => {
      this.series = parserie;
      this.cargando = false;
      console.log(parserie);
    });
  }

  Borrar(parserie: Parserie){
    Swal.fire({
      title: 'Esta seguro de borrar la serie?',
      text: `Serie: ${parserie.idserie}`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then(result => {
      if(result.value){
              this.parserieServices.BorrarSerie(parserie.idserie).subscribe(resp => {
                this.CargarSeries();
                Swal.fire('Serie Borrada',`${parserie.idserie} fue eliminado correctamente`,'success');
              });
      }
    })
  }

}
