import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';
import { CabeceraProforma } from '../../../models/cabeceraproforma.model';
import { ProformasService } from '../../../services/proformas.service';
import { FacturasService } from '../../../services/facturas.service';
import { CabeceraFactura } from 'src/app/models/cabecerafactura.model';
import { DetalleTransaction } from 'src/app/models/transaction.model';
import { DetalleFactura } from 'src/app/models/detallefactura.model';
import { DetalleProforma } from '../../../models/detalleproforma.model';
import { ParserieService } from '../../../services/parserie.service';
import { UsuariosService } from '../../../services/usuarios.service';
import { AuthService } from '../../../services/auth.service';

@Component({
  selector: 'app-listaproformas',
  templateUrl: './listaproformas.component.html',
  styles: [],
})
export class ListaproformasComponent implements OnInit {
  public proformas: CabeceraProforma[] = [];
  public cargando: boolean = true;
  cabeceraFactura: CabeceraFactura;
  public transaccion: DetalleTransaction[] = [];

  //Lista de productos agregados en la factura
  public transactions: DetalleTransaction[] = [];
  public detalles: DetalleProforma[] = [];
  empresa: string = localStorage.getItem('idemp');
  public roleUsuario = '';


  constructor(
    private facturasServices: FacturasService,
    private proformasService: ProformasService,
    private router: Router,
    private parserieService: ParserieService,
    private usuarioService: AuthService
  ) { }

  ngOnInit(): void {
    //console.log("object");
    this.CargarProformas();
    this.CargarUsuario();
  }

  //Cargar el id de usuario
  CargarUsuario(){
    this.roleUsuario = this.usuarioService.role;
  }

  CargarProformas() {
    //console.log("object2");
    this.cargando = true;
    this.proformasService
      .cargarProformas(this.empresa)
      .subscribe(({ cabproforma }) => {
        this.proformas = cabproforma;
        this.cargando = false;
        console.log('Este debe ser ' + this.proformas);
      });
  }

  //Metodo para actualizar la proforma Aprobar/Dar de baja/Reactivar
  Actualizar(id, estado) {
    const datos = {
      estado: estado,
    };
    Swal.fire({
      title: 'Se cambiara la proforma?\n' + estado,
      text: `Proforma: ${id}`,
      icon: 'info',
      showCancelButton: true,
      confirmButtonText: 'Continuar',
    }).then((result) => {
      if (result.value) {
        this.proformasService.actualizarProformas(id, datos).subscribe(
          (resp) => {
            Swal.fire({
              title: "Listo",
              text: "Proforma " + id + " Actualizada",
              icon: "success",
              preConfirm: () => { window.location.reload(); }
            });
          },
          (err) => {
            Swal.fire(
              'Error',
              'No se ha podido dar de baja la proforma intente mas tarde',
              'error'
            );
            console.log(err);
          }
        );
      }
    });
  }

  nueveCifras(number) {
    var numberOutput = Math.abs(number); /* Valor absoluto del nmero */
    var length = number.toString().length; /* Largo del nmero */ 
    var zero = "0"; /* String de cero */ 
    if (9 <= length) {
        if (number < 0) {
             return ("-" + numberOutput.toString()); 
        } else {
             return numberOutput.toString(); 
        }
    } else {
        if (number < 0) {
            return ("-" + (zero.repeat(9 - length)) + numberOutput.toString()); 
        } else {
            return ((zero.repeat(9 - length)) + numberOutput.toString()); 
        }
    }
}




  detalleFactura: DetalleFactura;

  Facturar(id) {
    this.router.navigateByUrl('dashboard/facturar/'+id);
  }
}
