import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { GLOBAL } from './global';
import { formaPago } from '../models/formaspago.model';
import { CargarFormasPagos } from '../interfaces/cargar.interface';
import { CrearFormaPago } from '../interfaces/crear-nuevaformapago.interface';

@Injectable({
  providedIn: 'root'
})
export class FormasPagoService {

  //public formasPago: formasPago[] = [];

  private url: string;

  constructor(private http: HttpClient) { this.url=GLOBAL.url; }

  cargarFormasPago(empresa: string){
    const urlService = `${this.url}parformapagos/${empresa}`;
    return this.http.get<CargarFormasPagos>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  cargarFormaPago(idforma: string){
    const urlService = `${this.url}parformapago/${idforma}`;
    return this.http.get<formaPago>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  crearFormaPago(formData: CrearFormaPago){
    const urlService = `${this.url}parformapago`;
    return this.http.post(urlService, formData, {
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  BorrarFormaPago(idFormaPago: string){
    const urlService = `${this.url}parformapago/delete/${idFormaPago}`; 
    return this.http.delete(urlService,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  actualizarFormasPago(formaPago: string, nuevoDatos: formaPago){
    const urlService = `${this.url}parformapago/${formaPago}`;
    return this.http.put(urlService, nuevoDatos,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }
}
