import { Component, OnInit } from '@angular/core';
import { Planestructura } from 'src/app/models/estrucuentas.model';
import { EstrucuentasService } from '../../services/estrucuentas.service';


@Component({
  selector: 'app-estrucuentas',
  templateUrl: './estrucuentas.component.html',
  styles: [
  ]
})
export class EstrucuentasComponent implements OnInit {

  constructor(private estrucuentasServices: EstrucuentasService) { }

  public planestructuras: Planestructura[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");

  ngOnInit(): void {
    console.log("Iniciando");
    this.CargarEstrucuentas();
  }

  CargarEstrucuentas(){
    //console.log("object2");
    this.cargando = true;
    this.estrucuentasServices.DevolverEstrucuentas(this.empresa).subscribe( ({planestructura}) => {
      this.planestructuras = planestructura;
      this.cargando = false;
      console.log(planestructura);
    });
  }
}
