import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GLOBAL } from './global';
import { CargarUnidades } from '../interfaces/cargar.interface';
import { CrearUnidad } from '../interfaces/crear-nuevaunidad.interface';
import { Unidad } from '../models/unidad.model';



@Injectable({
  providedIn: 'root'
})

export class UnidadService {

  constructor(private http: HttpClient) {
    this.url=GLOBAL.url;
    }

    private url: string;

    cargarUnidades(empresa: string){
      const urlService = `${this.url}parunidades/${empresa}`;
      return this.http.get<CargarUnidades>(urlService, {
        headers:{
          'Content-Type': 'Aplication/json',
          'Authorization': localStorage.getItem('token')
        }
      });
    }

    BorrarUnidad(idUnidad: string){
      const urlService = `${this.url}parunidad/delete/${idUnidad}`; 
      return this.http.delete(urlService,{
        headers:{
          'Authorization': localStorage.getItem('token')
        }
      });
    }

    //Servicio para guardar la unidad  

crearUnidad(formData: CrearUnidad){
  const urlService = `${this.url}parunidad`;
  return this.http.post(urlService, formData, {
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
  }

// servicio para cargar la unidad en el formulario

cargarUnidad(idunidad: string){
  const urlService = `${this.url}parunidad/${idunidad}`;
  console.log(urlService);
  return this.http.get<Unidad>(urlService, {
    headers:{
      'Content-Type': 'Aplication/json',
      'Authorization': localStorage.getItem('token')
    }
  });
  }

actualizarUnidad(idunidad: string, nuevoDatos: Unidad){
  const urlService = `${this.url}parunidad/${idunidad}`;
  return this.http.put(urlService, nuevoDatos,{
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
  }

}
