import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Planestructura } from '../models/estrucuentas.model';
import { GLOBAL } from './global';
import { CargarEstrucuentas } from '../interfaces/cargar.interface';
import { CrearEstrucuentas } from '../interfaces/crear-nuevaestrucuentas.interface';


@Injectable({
  providedIn: 'root'
})
export class EstrucuentasService {

  constructor(private http: HttpClient) { 
    this.url=GLOBAL.url;
  }
  public planestructura: Planestructura[] = [];

  private url: string;

  //Servicio para devolver impuestos especifico
  DevolverEstrucuentas(idEmpresa){
    const urlService = `${this.url}planestructura/${idEmpresa}`;
    return this.http.get<CargarEstrucuentas>(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

//Servicio para guardar la estructura de cuentas 
crearEstrucuentas(formData: CrearEstrucuentas){
  const urlService = `${this.url}planestructura`;
  return this.http.post(urlService, formData, {
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
}

cargarEstrucuentas(idempresa: string){
  const urlService = `${this.url}planestruc/${idempresa}`;
  console.log(urlService);
  return this.http.get<Planestructura>(urlService, {
    headers:{
      'Content-Type': 'Aplication/json',
      'Authorization': localStorage.getItem('token')
    }
  });

}

actualizarEstrucuentas(idempresa: string, nuevoDatos: Planestructura){
  const urlService = `${this.url}planestructura/${idempresa}`;
  return this.http.put(urlService, nuevoDatos,{
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
}


}
