import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CargarParSerie, CargarSeries } from '../interfaces/cargar.interface';
import { GLOBAL } from './global';
import { Parserie } from '../models/parserie.model';
import { CrearSerie } from '../interfaces/crear-nuevaserie.interface';


@Injectable({
  providedIn: 'root'
})
export class ParserieService {

  constructor(private http: HttpClient) {
    this.url=GLOBAL.url;
  }

  public parserie: Parserie[] = [];

  private url: string;

  //Metodo para obtener los datos de una serie en especifico
  obtenerDatosSerie(id: string){
    const urlService = `${this.url}parserie/${id}`;
    return this.http.get<Parserie>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    })
  }

  //Servicio para devolver series de la empresa
  DevolverSeries(idEmpresa){
    const urlService = `${this.url}parseries/${idEmpresa}`;
    return this.http.get<CargarSeries>(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  BorrarSerie(idSerie: string){
    const urlService = `${this.url}parserie/delete/${idSerie}`; 
    return this.http.delete(urlService,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

//Servicio para guardar la serie  

crearSerie(formData: CrearSerie){
  const urlService = `${this.url}parserie`;
  return this.http.post(urlService, formData, {
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
}

cargarSerie(idserie: string){
  const urlService = `${this.url}parserie/${idserie}`;
  console.log(urlService);
  return this.http.get<Parserie>(urlService, {
    headers:{
      'Content-Type': 'Aplication/json',
      'Authorization': localStorage.getItem('token')
    }
  });
}

actualizarSerie(idserie: string, nuevoDatos: Parserie){
  const urlService = `${this.url}parserie/${idserie}`;
  return this.http.put(urlService, nuevoDatos,{
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
}

}
