import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GLOBAL } from './global';
import { Usucajaserie } from '../models/usucajaserie.model';
import { CargarUsucajaserie, CargarUsuSerie } from '../interfaces/cargar.interface';
import { CrearUsucajaserie } from '../interfaces/crear-nuevousucajaserie.interface';





@Injectable({
  providedIn: 'root'
})
export class UsucajaserieService {

  constructor(private http: HttpClient) {
    this.url = GLOBAL.url;
  }

  public usucajaserie: Usucajaserie[] = [];

  private url: string;

  //Servicio para devolver impuestos especifico
  DevolverUsucajaserie(idEmpresa) {
    const urlService = `${this.url}usucajaseries/${idEmpresa}`;
    return this.http.get<CargarUsucajaserie>(urlService, {
      headers: {
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Servicio para devolver un a serie segun el usuario
  DevolverLaSerieDeUnUsuario(idusuario,idEmpresa) {
    const urlService = `${this.url}usucajaserie/${idusuario}/${idEmpresa}`;
    return this.http.get<Usucajaserie>(urlService,{
      headers: {
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    },);
  }

  BorrarUsucajaserie(sectabla: number) {
    const urlService = `${this.url}usucajaseries/delete/${sectabla}`;
    return this.http.delete(urlService, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }


  //Servicio para guardar el usuario - caja - serie  
  crearUsucajaserie(formData: CrearUsucajaserie) {
    const urlService = `${this.url}usucajaserie`;
    return this.http.post(urlService, formData, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  cargarUsucajaserie(idsectabla: number){
    const urlService = `${this.url}usucajaserieone/${idsectabla}`;
    console.log(urlService);
    return this.http.get<Usucajaserie>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }
  
  actualizarUsucajaserie(idsectabla: number, nuevoDatos: Usucajaserie){
    const urlService = `${this.url}usucajaserie/${idsectabla}`;
    return this.http.put(urlService, nuevoDatos,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }



}
