import { Component, OnInit } from '@angular/core';
import { Familia } from 'src/app/models/familias.model';
import Swal from 'sweetalert2';
import { FamiliasService } from '../../services/familias.service';


@Component({
  selector: 'app-familias',
  templateUrl: './familias.component.html',
  styles: [
  ]
})
export class FamiliasComponent implements OnInit {

  constructor(private familiasServices: FamiliasService) { }

  public familias: Familia[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");

  ngOnInit(): void {
    console.log("Iniciando");
    this.CargarFamilias();
  }

  CargarFamilias(){
    //console.log("object2");
    this.cargando = true;
    this.familiasServices.DevolverFamilias(this.empresa).subscribe( ({parfamilia}) => {
      this.familias = parfamilia;
      this.cargando = false;
      console.log(parfamilia);
    });
  }

  Borrar(parfamilia: Familia){
    Swal.fire({
      title: 'Esta seguro de borrar la familia?',
      text: `Familia: ${parfamilia.idfamilia}`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then(result => {
      if(result.value){
              this.familiasServices.BorrarFamilia(parfamilia.idfamilia).subscribe(resp => {
                this.CargarFamilias();
                Swal.fire('Familia Borrada',`${parfamilia.idfamilia} fue eliminado correctamente`,'success');
              });
      }
    })
  }

}
