import { Component, OnInit } from '@angular/core';
import { Precioclientes } from 'src/app/models/precioclientes.model';
import Swal from 'sweetalert2';
import { PrecioclientesService } from '../../services/precioclientes.service';


@Component({
  selector: 'app-precioclientes',
  templateUrl: './precioclientes.component.html',
  styles: [
  ]
})
export class PrecioclientesComponent implements OnInit {

  constructor(private precioclientesServices: PrecioclientesService) { }

  public preciocliente: Precioclientes[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");

  ngOnInit(): void {
    console.log("Iniciando");
    this.CargarPrecioclientes();
  }

  CargarPrecioclientes(){
    //console.log("object2");
    this.cargando = true;
    this.precioclientesServices.DevolverPrecioClientes(this.empresa).subscribe( ({precioclientes}) => {
      this.preciocliente = precioclientes;
      this.cargando = false;
      console.log(precioclientes);
    });
  }

  Borrar(precioclientes: Precioclientes){
    Swal.fire({
      title: 'Esta seguro de borrar el precio del Cliente por producto?',
      text: `Precio del cliente por producto: ${precioclientes.secprecli}`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then(result => {
      if(result.value){
              this.precioclientesServices.BorrarPrecioClientes(precioclientes.secprecli).subscribe(resp => {
                this.CargarPrecioclientes();
                Swal.fire('Precio del cliente por producto',`${precioclientes.secprecli} fue eliminado correctamente`,'success');
              });
      }
    })
  }

}
