import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { GLOBAL } from './global';
import { CrearFE } from '../interfaces/crear-facturaelectronica';

@Injectable({
  providedIn: 'root'
})
export class FacturacionelectronicaService {

  constructor(private http: HttpClient) { this.url = GLOBAL.url; this.token = localStorage.getItem('token'); }
  private url: string;
  private token = localStorage.getItem('token');



  facturarElectronicamenteUnicaFactura(secuencial: number) {
    const urlService = `${this.url}cabmovfac/fe/${secuencial}`;
    return this.http.get(urlService, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }, 'responseType': 'text'
    });
  }

  comprobarAutorizacionFactura(claveAcceso: number) {
    const urlService = `${this.url}comprobarAutorizacion/${claveAcceso}`;
    return this.http.get(urlService, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }
}
