import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ContplancuentasService } from 'src/app/services/contplancuentas.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-nuevo-contplancuentas',
  templateUrl: './nuevo-contplancuentas.component.html',
  styles: [
  ]
})
export class NuevoContplancuentasComponent implements OnInit {

  public plancuentasForm: FormGroup;
  
  constructor(private fb: FormBuilder,
    private contplancuentasService: ContplancuentasService, 
    private router: Router) { }

  ngOnInit(){ this.crearFormulario();}

    //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
    empresa: string = localStorage.getItem('idemp');

    crearFormulario = () => {
      this.plancuentasForm = this.fb.group({
        idempresa: [this.empresa],
        idcuenta: ['', Validators.required],
        nomcuenta: ['', Validators.required],
        nivelcuenta: ['Nivel-1', Validators.required],
        cuentapadre: ['', Validators.required],
        crepor: [localStorage.getItem('idcli')],
        modpor: [0],
        activo: [true],
        tipocuenta: [true, Validators.required],
      });
  
    }
  
    guardarPlacuentas() {
      this.contplancuentasService.crearPlancuentas(this.plancuentasForm.value).subscribe(resp => {
        console.log(resp);
        Swal.fire('Creado','Nueva cuenta creada correctamente','success');
        this.router.navigateByUrl('dashboard/contplancuentas');
      }, (err) => {Swal.fire('Error','No se ha podido ingresar la cuenta intente mas tarde','error');});
  
    }
  
    regresar() {
      this.router.navigateByUrl('dashboard/contplancuentas');
    }

}
