import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router, RouteReuseStrategy } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { Cliente } from 'src/app/models/cliente.model';
import { ClientesService } from 'src/app/services/clientes.service';
import Swal from 'sweetalert2';
import { CargarCliente } from '../../../interfaces/cargar.interface';

@Component({
  selector: 'app-cliente',
  templateUrl: './cliente.component.html',
  styles: [],
})
export class ClienteComponent implements OnInit {
  public clienteForm: FormGroup;
  empresa: string = localStorage.getItem('idemp');
  public cliente: Cliente;
  public clienteSubido: FormGroup;
  private searchResultSource = new BehaviorSubject<string>('');
  public contadorDeClientes: string = '';
  private codigoCliente: string = '';

  setSearchResults(_searchResult: string): void {
    this.searchResultSource.next(_searchResult);
  }

  constructor(
    private fb: FormBuilder,
    private clienteService: ClientesService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) { }

  crearFormulario = () => {
    this.clienteForm = this.fb.group({
      idempresa: [this.empresa],
      idcliente: [''],
      idencliente: ['', Validators.required],
      ruccicliente: ['', [Validators.required]],
      nomcliente: ['', Validators.required],
      dircliente: ['', Validators.required],
      telfcliente: ['', Validators.required],
      emailcliente: ['', [Validators.email, Validators.required]],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      idsucursal: ['SUC01'],
      activo: [true],
      tipocliente: ['Contado'],
    });
  };

  ngOnInit(): void {
    this.crearFormulario();
    this.CargarCliente();
  }

  validarCedula(cedula, tipoDocumento) {
    var cedulaValidada = this.clienteSubido['ruccicliente'].toString();
    console.log(cedulaValidada.length);
    if (tipoDocumento == 'Cedula') {
      if (cedulaValidada.length == 10) {
        //console.log('Esta entrando');
        this.clienteSubido = cedula;
        var numeroProvincia: number = parseInt(cedulaValidada[0] + cedulaValidada[1]);
        console.log(numeroProvincia);
        if (numeroProvincia < 0 || numeroProvincia > 24) {
          Swal.fire('Error', 'La provincia esta fuera del rango', 'error');
        } else if (cedulaValidada[2] < 6 && cedulaValidada[2] >= 0) {
          console.log('Holaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa');
          var resultado;
          var coeficiente = 2;
          var acumuladorResultado: number = 0;
          var decenaFinal = 0;
          var resultadoFinal;
          for (let i = 0; i <= 8; i++) {
            resultado = parseInt(cedulaValidada[i]) * coeficiente;
            if (coeficiente == 2) {
              coeficiente = 1
            } else {
              coeficiente = 2
            }
            if (resultado >= 10) {
              resultado = resultado - 9;
              acumuladorResultado = acumuladorResultado + resultado;
            } else {
              acumuladorResultado = acumuladorResultado + resultado;
            }
          }
          while (decenaFinal < acumuladorResultado) {
            decenaFinal = decenaFinal + 10;
          }
          resultadoFinal = decenaFinal - acumuladorResultado;
          if (resultadoFinal === parseInt(cedulaValidada[9])) {
            return true;
          } else {
            return false;
          }
        } else {
          return false;
        }
      } else {
        Swal.fire('Error', 'El tamao de la cedula no es correcto', 'error');
        return false;
      }
    } else if (tipoDocumento == 'RUC') {
      if (cedulaValidada.length == 13) {
        this.clienteSubido = cedula;
        var cedulaValidada = this.clienteSubido['ruccicliente'].toString();
        var numeroProvincia: number = parseInt(cedulaValidada[0] + cedulaValidada[1]);
        if (numeroProvincia < 0 || numeroProvincia > 24) {
          Swal.fire('Error', 'La provincia esta fuera del rango', 'error');
        } else if (cedulaValidada[2] < 6 && cedulaValidada[2] >= 0) {
          var resultado;
          var coeficiente = 2;
          var acumuladorResultado: number = 0;
          var decenaFinal = 0;
          var resultadoFinal;
          for (let i = 0; i <= 8; i++) {
            resultado = parseInt(cedulaValidada[i]) * coeficiente;
            if (coeficiente == 2) {
              coeficiente = 1
            } else {
              coeficiente = 2
            }
            if (resultado >= 10) {
              resultado = resultado - 9;
              acumuladorResultado = acumuladorResultado + resultado;
            } else {
              acumuladorResultado = acumuladorResultado + resultado;
            }
          }
          while (decenaFinal < acumuladorResultado) {
            decenaFinal = decenaFinal + 10;
          }
          resultadoFinal = decenaFinal - acumuladorResultado;
          if (resultadoFinal === parseInt(cedulaValidada[9])) {
            if ((cedulaValidada[10] == 0 && cedulaValidada[11] == 0 && cedulaValidada[12] == 1) || (cedulaValidada[10] == 0 && cedulaValidada[11] == 0 && cedulaValidada[12] == 2) || (cedulaValidada[10] == 0 && cedulaValidada[11] == 0 && cedulaValidada[12] == 3)) {
              return true;
            } else {
              return false;
            }
          } else {
            return false;
          }
        } else if (cedulaValidada[2] == 9) {
          console.log('Entro a RUC juridico');
          var resultadoRUC: number = 0;
          var coeficienteJuridico = '432765432';
          for (let i = 0; i <= 8; i++) {
            resultadoRUC = resultadoRUC + (parseInt(cedulaValidada[i]) * parseInt(coeficienteJuridico[i]));
          }
          var residuo = resultadoRUC % 11;
          var valorFinalComparado = 11 - residuo;
          if (residuo == 0) {
            valorFinalComparado = 0;
          } else if (residuo == 1) {
            valorFinalComparado = 1;
          }
          if (valorFinalComparado == cedulaValidada[9]) {
            return true;
          } else {
            return false;
          }
        } else if (cedulaValidada[2] == 6) {
          var resultadoRUC: number = 0;
          var coeficientePublico = '32765432';
          for (let i = 0; i <= 8; i++) {
            resultadoRUC = resultadoRUC + (parseInt(cedulaValidada[i]) * parseInt(coeficientePublico[i]));
          }
          var residuo = resultadoRUC % 11;
          var valorFinalComparado = 11 - residuo;
          if (residuo == 0) {
            valorFinalComparado = 0;
          } else if (residuo == 1) {
            valorFinalComparado = 1;
          }
          if (valorFinalComparado == cedulaValidada[8]) {
            return true;
          } else {
            return false;
          }
        } else {
          return false;
        }
      } else {
        Swal.fire('Error', 'El tamao del ruc no es correcto', 'error');
        return false;
      }
    } else {
      return true;
    }
  }

  CargarCliente() {
    this.activatedRoute.params.subscribe(({ id }) => {
      console.log(id);
      this.codigoCliente = id;
      console.log(this.codigoCliente);
    });
    this.clienteService.cargarCliente(this.codigoCliente).subscribe((resp) => {
      console.log(resp);
      const {
        idempresa,
        idcliente,
        idencliente,
        ruccicliente,
        nomcliente,
        dircliente,
        telfcliente,
        emailcliente,
        tipocliente,
      } = resp;
      //console.log(idcliente,idencliente,ruccicliente,nomcliente,dircliente,telfcliente,emailcliente,idsucursal);
      const crepor = 1;
      const modpor = 1;
      const idsucursal = 'SUC01';
      const activo = true;
      this.cliente = resp;
      this.clienteForm.setValue({
        idempresa,
        idcliente,
        idencliente,
        ruccicliente,
        nomcliente,
        dircliente,
        telfcliente,
        emailcliente,
        crepor,
        modpor,
        idsucursal,
        activo,
        tipocliente,
      });
      return resp;
    });
  }

  public ClienteFinal: Cliente;

  guardarCliente() {
    console.log('Guardar');
    this.clienteSubido = this.clienteForm.value;
    var tipoDocumento = this.clienteSubido['idencliente'];
    var cedula = this.clienteSubido['ruccicliente'];
    var condicionExistencia = 0;
    if (this.validarCedula(this.clienteSubido, tipoDocumento) === true) {
      this.clienteSubido = this.clienteForm.value;
      const datos = {
        ...this.clienteForm.value
      }
      console.log(datos);
      this.clienteService
        .actualizarClientes(
          datos.idcliente,
          datos
        )
        .subscribe(
          (resp) => {
            console.log(resp);
            Swal.fire('Actualizado', this.clienteForm.value['nomcliente'] + 'Cliente actualizado correctamente', 'success');
            this.router.navigateByUrl('dashboard/clientes');
          },
          (err) => {
            Swal.fire(
              'Error',
              'No se ha podido actualizar el cliente intente mas tarde',
              'error'
            );
            console.log(err);
          }
        );
    } else {
      Swal.fire('Error', 'La cdula o ruc no es vlida', 'error');
    }
  }

  regresar() {
    this.router.navigateByUrl('dashboard/clientes');
  }
}
