import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GLOBAL } from './global';
import { Caja } from '../models/caja.model';
import { CargarCajas } from '../interfaces/cargar.interface';
import { CrearCaja } from '../interfaces/crear-nuevacaja.interface';



@Injectable({
  providedIn: 'root'
})
export class CajaService {

  get headers(){
    return{
      header:{
        'Content-Type': 'Aplication/json'
      }
    }
  }
  constructor(private http: HttpClient) { 
    this.url=GLOBAL.url;
  }

  public caja: Caja[] = [];

  private url: string;

  //Servicio para devolver las cajas
  DevolverCajas(idEmpresa){
    const urlService = `${this.url}parcajas/${idEmpresa}`;
    return this.http.get<CargarCajas>(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  // Servicio para borrar las cajas
  BorrarCaja(idCaja: string){
    const urlService = `${this.url}parcaja/delete/${idCaja}`; 
    return this.http.delete(urlService,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Servicio para guardar la caja 

crearCaja(formData: CrearCaja){
  const urlService = `${this.url}parcaja`;
  return this.http.post(urlService, formData, {
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
}

// servicio para cargar la caja en el formulario

cargarCaja(idcaja: string){
  const urlService = `${this.url}parcaja/${idcaja}`;
  console.log(urlService);
  return this.http.get<Caja>(urlService, {
    headers:{
      'Content-Type': 'Aplication/json',
      'Authorization': localStorage.getItem('token')
    }
  });
}

actualizarCaja(idcaja: string, nuevoDatos: Caja){
  const urlService = `${this.url}parcaja/${idcaja}`;
  return this.http.put(urlService, nuevoDatos,{
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
}

}
