import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { SucursalService } from 'src/app/services/sucursal.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-nuevo-sucursal',
  templateUrl: './nuevo-sucursal.component.html',
  styles: [
  ]
})
export class NuevoSucursalComponent implements OnInit {
  public sucursalForm: FormGroup;
  constructor(private fb: FormBuilder,
    private sucursalService: SucursalService,
    private router: Router) { }

  ngOnInit(){ this.crearFormulario();}


  //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
  empresa: string = localStorage.getItem('idemp');

  crearFormulario = () => {
    this.sucursalForm = this.fb.group({
      idempresa: [this.empresa],
      idsucursal: ['', Validators.required],
      nomsucursal: ['', Validators.required],
      direccion: ['', Validators.required],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      activo: [true],
    });

  }

  guardarSucursal() {
    this.sucursalService.crearSucursal(this.sucursalForm.value).subscribe(resp => {
      console.log(resp);
      Swal.fire('Creado','Nueva sucursal creada correctamente','success');
      this.router.navigateByUrl('dashboard/sucursales');
    }, (err) => {Swal.fire('Error','No se ha podido ingresar la sucursal intente mas tarde','error');});
  }

  regresar() {
    this.router.navigateByUrl('dashboard/sucursales');
  }

}
