import { Component, OnInit } from '@angular/core';
import { ReportesProformasService } from '../../../services/reportes-proformas.service';


@Component({
  selector: 'app-proformas',
  templateUrl: './proformas.component.html',
  styleUrls: ['./proformas.component.css']
})
export class ProformasComponent implements OnInit {

  empresa: string = localStorage.getItem("idemp");
  fechaini = '';
  fechafin = '';
  estado = '';
  subtotal: number = 0.00;
  subtotaliva0: number = 0.00;
  iva0: number = 0.00; 
  subtotaliva12: number = 0.00;
  iva12: number = 0.00; 
  total: number = 0.00;

  constructor(private reporteProformasService: ReportesProformasService,) { }


  public proformas:any = [];

  ngOnInit(): void {
  }

  CargarReporteProformas(fechaini,fechafin,estado){
    console.log(fechaini,fechafin,estado);
    this.subtotal = 0.00;  
    this.subtotaliva0 = 0.00;
    this.iva0 = 0.00;
    this.subtotaliva12 = 0.00;  
    this.iva12 = 0.00;  
    this.total = 0.00;  
    this.reporteProformasService.ReporteProformasFechas(this.empresa,fechaini,fechafin,estado).subscribe(resp=>{
      this.proformas = resp;
      for (let index = 0; index < this.proformas.length; index++){
        const element = this.proformas[index];
        this.subtotal = this.subtotal + Number(element.subtotal);
        this.subtotaliva0 = this.subtotaliva0 + Number(element.subtotaliva0);
        this.iva0 = this.iva0 + Number(element.iva0);
        this.subtotaliva12 = this.subtotaliva12 + Number(element.subtotaliva12);
        this.iva12 = this.iva12 + Number(element.iva12);
        this.total = this.total + Number(element.total);  
      }
    })
  }
}
