import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ParserieService } from 'src/app/services/parserie.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-nuevo-serie',
  templateUrl: './nuevo-serie.component.html',
  styles: [
  ]
})
export class NuevoSerieComponent implements OnInit {

  public serieForm: FormGroup;

  constructor(private fb: FormBuilder,
    private parserieService: ParserieService,
    private router: Router) { }

  ngOnInit(){ this.crearFormulario();}

    //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
    empresa: string = localStorage.getItem('idemp');

    crearFormulario = () => {
      this.serieForm = this.fb.group({
        idempresa: [this.empresa],
        idsucursal: ['SUC01'],
        idserie: ['', Validators.required],
        tipserie: ['', Validators.required],
        ptoemision: ['', Validators.required],
        ptofacturacion: ['', Validators.required],
        secuencia: [0],
        crepor: [localStorage.getItem('idcli')],
        modpor: [0],
        activo: [true],
      });
  
    }
  
    guardarSerie() {
      this.parserieService.crearSerie(this.serieForm.value).subscribe(resp => {
        console.log(resp);
        Swal.fire('Creado','Nueva serie creada correctamente','success');
        this.router.navigateByUrl('dashboard/serie');
      }, (err) => {Swal.fire('Error','No se ha podido ingresar la serie intente mas tarde','error');});
    }
  
    regresar() {
      this.router.navigateByUrl('dashboard/serie');
    }
  

}
