import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';
import { FacturasService } from '../../../services/facturas.service';
import { CabeceraFactura } from '../../../models/cabecerafactura.model';
import { FacturacionelectronicaService } from '../../../services/facturacionelectronica.service';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { DataSource } from '@angular/cdk/collections';
import { Observable, ReplaySubject } from 'rxjs';

export interface PeriodicElement {
  secmovcab: number;
  idcliente: string;
  numfactura: string;
  numautosri: string;
  EstadoAutorizacionSRI: string;
  EstadoRecepcionSRI: string;
}



@Component({
  selector: 'app-listafacturas',
  templateUrl: './listafacturas.component.html',
  styleUrls: [
    './listafacturas.component.css'
  ]
})

export class ListafacturasComponent implements OnInit, AfterViewInit {

  filterCodigo = '';
  filterCliente = '';
  filterNumeroFactura = '';
  filterNumeroAutorizacion = '';
  filterEstadoAutorizacion = '';
  filterEstadoRecepcion = '';




  constructor(private facturasService: FacturasService,
    private router: Router, private feservice: FacturacionelectronicaService) {
    this.CargarFacturas();
  }

  public facturas: CabeceraFactura[] = [];

  public cargando: boolean = true;

  facturasDatagrid: facturasInterface[] = [];

  ELEMENT_DATA: PeriodicElement[] = [];

  empresa: string = localStorage.getItem('idemp');

  @ViewChild(MatPaginator) paginator: MatPaginator;

  ngAfterViewInit() {
    //this.dataSource.paginator = this.paginator;
  }

  pageActual: number = 1;
  ngOnInit(): void {
    //console.log("object");
    this.CargarFacturas();
  }

  CargarFacturas() {
    //console.log("object2");
    this.cargando = true;
    this.facturasService
      .cargarFacturas(this.empresa)
      .subscribe(({ cabmovfac }) => {
        this.facturas = cabmovfac;
        this.facturasDatagrid = cabmovfac;
        this.cargando = false;
      });
  }

  //Metodo para actualizar la proforma Aprobar/Dar de baja/Reactivar
  Actualizar(id, estado) {
    const datos = {
      estado: estado,
    };
    Swal.fire({
      title: 'Se cambiara a estado ' + estado + ' \n Desea continar?',
      text: `Factura: ${id}`,
      icon: 'info',
      showCancelButton: true,
      confirmButtonText: 'Continuar',
    }).then((result) => {
      if (result.value) {
        this.facturasService.actualizarFacturas(id, datos).subscribe(
          (resp) => {
            Swal.fire({
              title: "Listo",
              text: "Factura" + id + " Actualizada",
              icon: "success",
              preConfirm: () => { window.location.reload(); }
            });
          },
          (err) => {
            Swal.fire(
              'Error',
              'No se ha anular la factura intente mas tarde',
              'error'
            );
            console.log(err);
          }
        );
      }
    });
  }

  FacturaElectronica(id) {
    this.feservice.facturarElectronicamenteUnicaFactura(id).subscribe((resp) => {
      console.log(resp);
    })
  }

  ComprobarAutorizacionFacturaElectronica(id, secuencial) {
    const datos = {
      EstadoAutorizacionSRI: 'AUTORIZADO',
      EstadoRecepcionSRI: 'RECIBIDA'
    };
    this.feservice.comprobarAutorizacionFactura(id).subscribe((resp) => {
      if (resp['message'] == 'La factura ha sido autorizada y enviada.') {
        console.log('OK');
        this.facturasService.actualizarFacturas(secuencial, datos).subscribe(resp => {
          Swal.fire({
            title: "Listo",
            text: "Factura" + id + " Actualizada",
            icon: "success",
            preConfirm: () => { window.location.reload(); }
          });
        })
      } else {
        Swal.fire({
          title: "Error",
          text: "La factura aun no ha sido autorizada.",
          icon: "error"
        });
      }
    })
  }

  DescargarXML(id) {
    id = 'factura358.xml';
    this.facturasService.imprimirXML(id).subscribe();
  }
}

export interface facturasInterface {
  idempresa: string;
  idsucursal: string;
  idcaja: string;
  idserie: string;
  secmovcab: number;
  idcliente: string;
  fechaingreso: any;
  fechaaprob: any;
  numfactura: string;
  numautosri: string;
  fechaautsri: string;
  subsindesc: number;
  descuento: number;
  subtotal: number;
  subtotaliva0: number;
  subtotaliva12: number;
  iva0: number;
  iva12: number;
  total: number;
  crepor: number;
  modpor: number;
  createdAt: string;
  updatedAt: string;
  estado: string;
  secproforma: number;
  porcdescuento: string;
  estadocobro: string;
  valorcobro: number;
  comentario: string;
}