import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalDismissReasons, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AuthService } from '../../services/auth.service';
import { Usuarios } from '../../models/usuariom.model';
import { UsuariosService } from '../../services/usuarios.service';
declare function customInitFunctions();
declare function customSidebar();
declare function sidebar2();
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styles: [
  ]
})
export class HeaderComponent implements OnInit {

  public nombre: string;
  public email: string;

  constructor(private router: Router,
    private authService: AuthService,
    private usuariosService: UsuariosService,
    private modal: NgbModal) {
      this.CargarUsuario();
   }

  ngOnInit(): void {
    customSidebar();
    //sidebar2();
    
  }

  salir(){
    localStorage.clear();
    window.location.href="login";
  }

  PantallaDeConfiguraciones(contenido){
     this.modal.open(contenido, { size: 'xl', centered: true, scrollable: true });
  }

  CargarUsuario(){
    this.usuariosService.cargarUsuario(parseInt(this.authService.id)).subscribe((resp) => {
      const {
        idempresa,
        idusuario,
        usuario,
        idrol,
        activo,
        crepor,
        nomusuario,
        emailusuario
      } = resp;
      this.nombre = nomusuario;
      this.email = emailusuario;
    });
  }
}
