import { Component, OnInit } from '@angular/core';
import { Usucajaserie } from 'src/app/models/usucajaserie.model';
import Swal from 'sweetalert2';
import { UsucajaserieService } from '../../services/usucajaserie.service';

@Component({
  selector: 'app-usucajaserie',
  templateUrl: './usucajaserie.component.html',
  styles: [
  ]
})
export class UsucajaserieComponent implements OnInit {

  constructor(private usucajaserieServices: UsucajaserieService) { }

  public usucajaseries: Usucajaserie[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");

  ngOnInit(): void {
    console.log("Iniciando");
    this.CargarUsucajaserie();

  }

  CargarUsucajaserie(){
    //console.log("object2");
    this.cargando = true;
    this.usucajaserieServices.DevolverUsucajaserie(this.empresa).subscribe( ({usucajaserie}) => {
      this.usucajaseries = usucajaserie;
      this.cargando = false;
      console.log(usucajaserie);
    });
  }

  Borrar(usucajaserie: Usucajaserie){
    Swal.fire({
      title: 'Esta seguro de borrar la asignacin de serie y caja del usuario?',
      text: `Asignacin de Serie y Cajas del Usuario: ${usucajaserie.sectabla}`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then(result => {
      if(result.value){
              this.usucajaserieServices.BorrarUsucajaserie(usucajaserie.sectabla).subscribe(resp => {
                this.CargarUsucajaserie();
                Swal.fire('Asignacin de serie y caja del usuario Borrada',`${usucajaserie.sectabla} fue eliminado correctamente`,'success');
              });
      }
    })
  }




}
