import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ProductosService } from '../../../services/productos.service';
import { FamiliasService } from '../../../services/familias.service';
import { CargarFamilias } from '../../../interfaces/cargar.interface';
import { Familia } from '../../../models/familias.model';
import { UnidadService } from '../../../services/unidad.service';
import { Unidad } from '../../../models/unidad.model';
import { ImpuestosService } from '../../../services/impuestos.service';
import { Impuesto } from 'src/app/models/impuesto.model';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-nuevo-productos',
  templateUrl: './nuevo-productos.component.html',
  styles: [
  ]
})
export class NuevoProductosComponent implements OnInit {

  public productosForm: FormGroup;
  public familias: Familia[] = [];
  public unidades: Unidad[] = [];
  public impuestos: Impuesto[] = [];

  //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
  empresa: string = localStorage.getItem('idemp');

  constructor(private fb: FormBuilder,
    private productosService: ProductosService,
    private router: Router,
    private familiasServicie: FamiliasService,
    private unidadesService: UnidadService,
    private impuestoServices: ImpuestosService) { }

  ngOnInit() { this.crearFormulario(); this.CargarFamilias(); this.CargarUnidades(); this.CargarImpuestos();}

  crearFormulario = () => {
    this.productosForm = this.fb.group({
      idempresa: [this.empresa],
      nomproducto: ['', Validators.required],
      nomprodcomercial: ['', Validators.required],
      impnomcomercial: ['1'],
      idfamilia: [''],
      idunidad: [''],
      tipproducto:[''],
      idimpuesto: [''],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      idsucursal: ['SUC01'],
      activo: [true],
      precioprodu: ['', Validators.required]
    });
  }

  CargarFamilias() {
    this.familiasServicie.CargarFamilias(this.empresa).subscribe(({parfamilia}) => {
      this.familias = parfamilia;
    });
  }

  CargarUnidades(){
    this.unidadesService.cargarUnidades(this.empresa).subscribe( ({parunidad}) => {
      this.unidades = parunidad;
    });
  }

  CargarImpuestos(){
    this.impuestoServices.DevolverImpuestos(this.empresa).subscribe( ({parimpuesto}) => {
      this.impuestos = parimpuesto;
      console.log(parimpuesto);
    });
  }

  guardarProducto() {
    this.productosService.crearProducto(this.productosForm.value).subscribe(resp => {
      console.log(resp);
      Swal.fire('Creado','Nuevo producto creado correctamente','success');
      this.router.navigateByUrl('dashboard/productos');
    }, (err) => {Swal.fire('Error','No se ha podido ingresar el producto intente mas tarde','error');});
  }

  regresar() {
    this.router.navigateByUrl('dashboard/productos');
  }
}
