import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Familia } from 'src/app/models/familias.model';
import { Impuesto } from 'src/app/models/impuesto.model';
import { Unidad } from 'src/app/models/unidad.model';
import { FamiliasService } from 'src/app/services/familias.service';
import { ImpuestosService } from 'src/app/services/impuestos.service';
import { ProductosService } from 'src/app/services/productos.service';
import { UnidadService } from 'src/app/services/unidad.service';
import { Producto } from 'src/app/models/productos.model';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-actualizar-productos',
  templateUrl: './actualizar-productos.component.html',
  styles: [
  ]
})
export class ActualizarProductosComponent implements OnInit {

  public productosForm: FormGroup;
  public familias: Familia[] = [];
  public unidades: Unidad[] = [];
  public impuestos: Impuesto[] = [];
  private codigoProducto: string = '';
  public producto: Producto;
  public formaSubido: FormGroup;

  //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
  empresa: string = localStorage.getItem('idemp');

  constructor(private fb: FormBuilder,
    private productosService: ProductosService,
    private router: Router,
    private familiasServicie: FamiliasService,
    private unidadesService: UnidadService,
    private impuestoServices: ImpuestosService,
    private activatedRoute: ActivatedRoute) { }

  ngOnInit(): void {
    this.crearFormulario();
    this.CargarFamilias(); 
    this.CargarUnidades(); 
    this.CargarImpuestos();
    this.CargarProductos();
  }

  //Cracndo formulario de productosForm para hacer el inicializar

  crearFormulario = () => {
    this.productosForm = this.fb.group({
      idempresa: [this.empresa],
      idproducto: [''],
      nomproducto: ['', Validators.required],
      nomprodcomercial: ['', Validators.required],
      impnomcomercial: ['1'],
      idfamilia: [''],
      idunidad: [''],
      tipproducto:[''],
      idimpuesto: [''],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      idsucursal: ['SUC01'],
      activo: [true],
      precioprodu: ['', Validators.required]
    });
  }

  //Funcion para cargar las familias

  CargarFamilias() {
    this.familiasServicie.CargarFamilias(this.empresa).subscribe(({parfamilia}) => {
      this.familias = parfamilia;
    });
  }

  //Funcion para cargar las unidades

  CargarUnidades(){
    this.unidadesService.cargarUnidades(this.empresa).subscribe( ({parunidad}) => {
      this.unidades = parunidad;
    });
  }

  //Funcion para cargar las impuestos

  CargarImpuestos(){
    this.impuestoServices.DevolverImpuestos(this.empresa).subscribe( ({parimpuesto}) => {
      this.impuestos = parimpuesto;
      console.log(parimpuesto);
    });
  }

  regresar() {
    this.router.navigateByUrl('dashboard/productos');
  }

  //Funcion para cargar los datos iniciales del producto segun el id que se encuentra en el link

  CargarProductos() {
    this.activatedRoute.params.subscribe(({ id }) => {
      this.codigoProducto = id;
    });
    this.productosService.cargarProductoPorID(this.codigoProducto).subscribe((resp) => {
      console.log(resp);
      const {
        idempresa,
        idproducto,
        nomproducto,
        nomprodcomercial,
        idfamilia,
        idunidad,
        tipproducto,
        idimpuesto,
        precioprodu,
        activo,
        idsucursal,
        crepor,
      } = resp;
      const impnomcomercial = '1';
      const modpor = 1;
      this.producto = resp;
      this.productosForm.setValue({
        idempresa,
        idproducto,
        nomproducto,
        nomprodcomercial,
        idfamilia,
        idunidad,
        tipproducto,
        idimpuesto,
        precioprodu,
        impnomcomercial,
        crepor,
        modpor,
        idsucursal,
        activo,
      });
      return resp;
    });
  }

  actualizarProducto() {
    console.log('Guardar');
    this.formaSubido = this.productosForm.value;
    const datos = {
      ...this.productosForm.value
    } 
    console.log(datos);
    this.productosService
      .actualizarProducto(
        datos.idproducto,
        datos
      )
      .subscribe(
        (resp) => {
          console.log(resp);
          Swal.fire('El producto ', this.productosForm.value['nomproducto'] + ' fue actualizado correctamente', 'success');
          this.router.navigateByUrl('dashboard/productos');
        },
        (err) => {
          Swal.fire(
            'Error',
            'No se ha podido actualizar el producto intente mas tarde',
            'error'
          );
          console.log(err);
        }
      );
  }


}
