import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CajaService } from 'src/app/services/caja.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-actualizar-caja',
  templateUrl: './actualizar-caja.component.html',
  styles: [
  ]
})

export class ActualizarCajaComponent implements OnInit {

  public cajaForm: FormGroup;
  private codigoCaja: string = '';
  public cajaSubida: FormGroup;

  constructor(private fb: FormBuilder,
    private cajaService: CajaService,
    private router: Router,
    private activatedRoute: ActivatedRoute) { }

  ngOnInit(){ this.crearFormulario();
  this.cargarCaja();
  }


  //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
  empresa: string = localStorage.getItem('idemp');

  crearFormulario = () => {
    this.cajaForm = this.fb.group({
      idempresa: [this.empresa],
      idsucursal: ['SUC01'],
      idcaja: ['', Validators.required],
      nomcaja: ['', Validators.required],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      activo: [true],
    });

  }

  cargarCaja() {
    this.activatedRoute.params.subscribe(({ id }) => {
      this.codigoCaja = id;
      console.log(this.codigoCaja);
    });
    this.cajaService.cargarCaja(this.codigoCaja).subscribe((resp) => {
      console.log(resp);
      const {
        idempresa,
        idsucursal,
        idcaja,
        nomcaja,
        crepor,
        activo
      } = resp;
      const modpor = [localStorage.getItem('idcli')];
      this.cajaForm.setValue({
        idempresa,
        idsucursal,
        idcaja,
        nomcaja,
        crepor,
        modpor,
        activo,
      });
      return resp;
    });
  }

  actualizarCaja() {
    console.log('Guardar');
    this.cajaSubida = this.cajaForm.value;
    const datos = {
      ...this.cajaForm.value
    } 
    console.log(datos);
    this.cajaService
      .actualizarCaja(
        datos.idcaja,
        datos
      )
      .subscribe(
        (resp) => {
          console.log(resp);
          Swal.fire('Actualizado ', this.cajaForm.value['idcaja'] + ' caja actualizada correctamente', 'success');
          this.router.navigateByUrl('dashboard/caja');
        },
        (err) => {
          Swal.fire(
            'Error',
            'No se ha podido actualizar la caja intente mas tarde',
            'error'
          );
          console.log(err);
        }
      );
  }

  

  regresar() {
    this.router.navigateByUrl('dashboard/caja');
  }


}
