import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';
import { formaPago } from '../../models/formaspago.model';
import { FormasPagoService } from '../../services/formas-pago.service';
import { CargarFormasPagos } from '../../interfaces/cargar-formas.interface';

@Component({
  selector: 'app-formas-pago',
  templateUrl: './formas-pago.component.html',
  styles: [
  ]
})
export class FormasPagoComponent implements OnInit {

  public totalClientes: number = 0;
  public formasPagos: formaPago[] = [];
  public cargando: boolean = true;

  constructor(private formasPagoService: FormasPagoService,
    private router: Router) { }

    empresa: string = localStorage.getItem("idemp");

  ngOnInit(): void {
    this.CargarFormasPagos();
  }

  CargarFormasPagos(){
    this.cargando = true;
    this.formasPagoService.cargarFormasPago("EMP01").subscribe( ({parformapago}) => {
      this.formasPagos = parformapago;
      this.cargando = false;
  });
  }

  Borrar(formaPago: formaPago){
    Swal.fire({
      title: 'Esta seguro de borrar la forma de pago?',
      text: `Cliente: ${formaPago.nomformapago}`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then(result => {
      if(result.value){
              this.formasPagoService.BorrarFormaPago(formaPago.idformapago).subscribe(resp => {
                this.CargarFormasPagos();
                console.log(resp); 
                Swal.fire('Forma de pago borrada',`${formaPago.nomformapago} fue eliminado correctamente`,'success');
              });
      }
    })
  }
}
