import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GLOBAL } from './global';
import { CargarUsuarios, UsuarioInterfaz } from '../interfaces/cargar.interface';
import { Usuarios } from '../models/usuariom.model';
import { CrearUsuario } from '../interfaces/crear-nuevousuario.interface';


@Injectable({
  providedIn: 'root'
})
export class UsuariosService {

  constructor(private http: HttpClient) {
    this.url=GLOBAL.url;
    }

    private url: string;

    devolverUsuarios(empresa: string){
      const urlService = `${this.url}usuarios/${empresa}`;
      return this.http.get<CargarUsuarios>(urlService, {
        headers:{
          'Content-Type': 'Aplication/json',
          'Authorization': localStorage.getItem('token')
        }
      });
    }

    borrarUsuarios(idUsuario: number){
      const urlService = `${this.url}usuario/delete/${idUsuario}`; 
      return this.http.delete(urlService,{
        headers:{
          'Authorization': localStorage.getItem('token')
        }
      });
    }

    //Servicio para guardar el usuarios  

crearUsuario(formData: CrearUsuario){
  const urlService = `${this.url}usuario`;
  console.log(formData)
  return this.http.post(urlService, formData, {
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
  }

  // servicio para cargar el usuario en el formulario

cargarUsuario(idusuario: number){
  const urlService = `${this.url}usuarione/${idusuario}`;
  return this.http.get<Usuarios>(urlService, {
    headers:{
      'Content-Type': 'Aplication/json',
      'Authorization': localStorage.getItem('token')
    }
  });
  }

  cargarUsuarioFinal(idusuario: number){
    const urlService = `${this.url}usuarione/${idusuario}`;
    return this.http.get<UsuarioInterfaz>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
    }

actualizarUsuario(idusuario: number, nuevoDatos: Usuarios){
  const urlService = `${this.url}usuario/${idusuario}`;
  return this.http.put(urlService, nuevoDatos,{
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
  }

}
