import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { UsuariosService } from 'src/app/services/usuarios.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-actualizar-usuarios',
  templateUrl: './actualizar-usuarios.component.html',
  styles: [
  ]
})
export class ActualizarUsuariosComponent implements OnInit {

  public usuarioForm: FormGroup;
  public usuarioSubido: FormGroup;
  private codigoUsuario: number;

  constructor(private fb: FormBuilder,
    private usuariosService: UsuariosService,
    private router: Router,
    private activatedRoute: ActivatedRoute) { }

  ngOnInit(){ this.crearFormulario();
  this.cargarUsuario();}

    //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
    empresa: string = localStorage.getItem('idemp');

    crearFormulario = () => {
      this.usuarioForm = this.fb.group({
        idempresa: [this.empresa],
        idusuario:[0],
        usuario: ['', Validators.required],
        password: ['', Validators.required],
        idrol: [1],
        activo: [true],
        crepor: [localStorage.getItem('idcli')],
        modpor: [0],
        nomusuario: ['', Validators.required],
        emailusuario: ['', Validators.required],
      });

    }

    cargarUsuario() {
      this.activatedRoute.params.subscribe(({ id }) => {
        this.codigoUsuario = id;
        console.log(this.codigoUsuario);
      });
      this.usuariosService.cargarUsuario(this.codigoUsuario).subscribe((resp) => {
        const {
          idempresa,
          idusuario,
          usuario,
          password,
          idrol,
          activo,
          crepor,
          nomusuario,
          emailusuario
        } = resp;
        const modpor = [localStorage.getItem('idcli')];
        this.usuarioForm.setValue({
          idempresa,
          idusuario,
          usuario,
          password,
          idrol,
          activo,
          crepor,
          modpor,
          nomusuario,
          emailusuario
        });
        return resp;
      });
    }
  
    actualizarUsuario() {
      console.log('Guardar');
      this.usuarioSubido = this.usuarioForm.value;
      const datos = {
        ...this.usuarioForm.value
      } 
      console.log(datos);
      this.usuariosService
        .actualizarUsuario(
          datos.idusuario,
          datos
        )
        .subscribe(
          (resp) => {
            console.log(resp);
            Swal.fire('Actualizado ', this.usuarioForm.value['idusuario'] + ' usuario actualizada correctamente', 'success');
            this.router.navigateByUrl('dashboard/usuarios');
          },
          (err) => {
            Swal.fire(
              'Error',
              'No se ha podido actualizar el usuario intente mas tarde',
              'error'
            );
            console.log(err);
          }
        );
    }

    regresar() {
      this.router.navigateByUrl('dashboard/usuarios');
    }

}
