import { Injectable } from '@angular/core';
import { CabeceraProforma } from '../models/cabeceraproforma.model';
import { HttpClient } from '@angular/common/http';
import { CargarFacturasCabeceras, CargarProformasCabeceras, IngresarProformasCabeceras, CargarProformasDetalle, CargarProformaCabecera, CargarDetallesProformas } from '../interfaces/cargar.interface';
import { GLOBAL } from './global';
import { DetalleProforma } from '../models/detalleproforma.model';

@Injectable({
  providedIn: 'root'
})
export class ProformasService {

  constructor(private http: HttpClient) {
    this.url = GLOBAL.url;
  }

  public proformas: CabeceraProforma[] = [];

  private url: string;

  get headers() {
    return {
      header: {
        'Content-Type': 'Aplication/json'
      }
    }
  }

  //localhost:8010/api/productos/EMP01

  cargarProformas(empresa: string) {
    const urlService = `${this.url}cabproformas/${empresa}`;
    return this.http.get<CargarProformasCabeceras>(urlService, {
      headers: {
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Obtener todas las proformas
  obtenerProforma(id: string) {
    const urlService = `${this.url}cabproformas/${id}`;
    return this.http.get<CargarProformasDetalle>(urlService, {
      headers: {
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Obtener una unica proforma
  obtenerUnaProforma(id: string) {
    const urlService = `${this.url}cabproforma/${id}`;
    return this.http.get<CabeceraProforma>(urlService, {
      headers: {
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  actualizarProformas(proforma: string, nuevoDatos: any) {
    const urlService = `${this.url}cabproforma/${proforma}`;
    return this.http.put(urlService, nuevoDatos, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  crearProformas(proforma: CabeceraProforma) {
    const urlService = `${this.url}cabproforma`;
    return this.http.post<IngresarProformasCabeceras>(urlService, proforma, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }


  //Obtener todos los detalles de una proforma **Este servicio necesita verificacion de EMPRESA**
  obtenerDetallesDeUnaProforma(id: number) {
    const urlService = `${this.url}detproformas/${id}`;
    return this.http.get<CargarDetallesProformas>(urlService, {
      headers: {
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  agregarDetalle(detalle: DetalleProforma) {
    const urlService = `${this.url}detproforma`;
    return this.http.post(urlService, detalle, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }
}
