import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { CuentaContable } from 'src/app/models/conplancuentas';
import { CuentasService } from 'src/app/services/cuentas.service';
import { ImpuestosService } from 'src/app/services/impuestos.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-nuevo-impuestos',
  templateUrl: './nuevo-impuestos.component.html',
  styles: [
  ]
})
export class NuevoImpuestosComponent implements OnInit {

  public impuestoForm: FormGroup;
  public cuentasContables: CuentaContable[] = [];

  constructor(private fb: FormBuilder,
    private cuentasService: CuentasService,
    private impuestosService: ImpuestosService,
    private router: Router) 
    { 
      this.CargarCuentas(this.empresa);
    }

    CargarCuentas(idempres: string){
      this.cuentasService.CargarCuentas(this.empresa).subscribe(({contplancuentas})=>{
        this.cuentasContables = contplancuentas;
      });
    }

  ngOnInit(){ this.crearFormulario();}

    //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
    empresa: string = localStorage.getItem('idemp');

    crearFormulario = () => {
      this.impuestoForm = this.fb.group({
        idempresa: [this.empresa],
        idimpuesto: ['', Validators.required],
        fechaini: ['', Validators.required],
        fechafin: ['', Validators.required],
        nomimpuesto: ['', Validators.required],
        idcuenta: ['', Validators.required],
        crepor: [localStorage.getItem('idcli')],
        modpor: [0],
        idsucursal: ['SUC01'],
        activo: [true],
        porcimpuesto: [0],
        codigoSRI: ['', Validators.required],
        codporcentajeSRI: ['', Validators.required],
      });
  
    }
  
    guardarImpuesto() {
      this.impuestosService.crearImpuesto(this.impuestoForm.value).subscribe(resp => {
        console.log(resp);
        Swal.fire('Creada','Nuevo impuesto creado correctamente','success');
        this.router.navigateByUrl('dashboard/impuesto');
      }, (err) => {Swal.fire('Error','No se ha podido ingresar el impuesto intente mas tarde','error');});
    }
  
    regresar() {
      this.router.navigateByUrl('dashboard/impuesto');
    }
}
