import { Component, OnInit, ViewChild } from '@angular/core';
import { ProductosService } from '../../services/productos.service';
import { Producto } from '../../models/productos.model';
import Swal from 'sweetalert2';
import { Router } from '@angular/router';
declare function customInitFunctions();
declare function sidebar2();
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { DataSource } from '@angular/cdk/collections';
import { Observable, ReplaySubject } from 'rxjs';


@Component({
  selector: 'app-productos',
  templateUrl: './productos.component.html',
  styles: [
  ]
})
export class ProductosComponent implements OnInit {

  public productos: Producto[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");

  constructor( private productosServices: ProductosService,
    private router: Router) { }

    filterProductosId = '';
    filterProNombre = '';
    filterProPrecio = '';
    filterProFamilia = '';
    filterProUnidad = '';
    filterProTipo = '';
    filterProImpuesto = '';

  @ViewChild(MatPaginator) paginator: MatPaginator;

  pageActual: number = 1;

  ngOnInit(): void {
    //console.log("object");
    this.CargarProductos();
  }

  CargarProductos(){
    //console.log("object2");
    this.cargando = true;
    this.productosServices.cargarProductos(this.empresa).subscribe( ({producto}) => {
      this.productos = producto;
      this.cargando = false;
      console.log(producto);
    });
  }

  Borrar(producto: Producto){
    Swal.fire({
      title: 'Esta seguro de borrar el producto?',
      text: `Producto: ${producto.idproducto}`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then(result => {
      if(result.value){
              this.productosServices.BorrarProducto(producto.idproducto).subscribe(resp => {
                this.CargarProductos();
                Swal.fire('Producto Borrado',`${producto.idproducto} fue eliminado correctamente`,'success');
              });
      }
    })
  }
}
