  //Cancelar la factura
  CancelarFactura() {
    this.subtotal0 = 0.00;
    this.subtotal12 = 0.000;
    this.subtotal = 0.00;
    this.iva = 0.00;
    this.descuento = 0.00;
    this.transactions = [];
    this.total = 0.00;
  }

  //Metodo para guardar factura en la base de datos
  GuardarFactura() {
    if (this.total == 0.00) {
      console.log('Su factura no tiene ningun producto');
    } else {
      console.log('Guardando la factura...');
    }
  }

  //Funcion para borrar un producto de la lista
  Actualizar(numero) {
    this.subtotal0 = this.subtotal0 - this.transactions[numero].subtotaliva0;
    this.subtotal12 = this.subtotal12 - this.transactions[numero].subtotaliva12;
    this.subtotal = this.subtotal - this.transactions[numero].subtotal;
    this.iva = this.iva - this.transactions[numero].iva12;
    this.total = this.total - this.transactions[numero].total;
    if (this.subtotal0 == -0.00) {
      this.subtotal0 = 0;
    } else if (this.subtotal12 == -0.00) {
      this.subtotal12 = 0;
    }
    else if (this.subtotal == -0.00) {
      this.subtotal = 0;
    }
    else if (this.iva == -0.00) {
      this.iva = 0;
    }
    else if (this.total == -0.00) {
      this.total = 0;
    }
    this.transactions.splice(numero, 1);
  }

  //Metodo para cargar cliente recibe la cedula del cliente que ingresa el cajero
  CargarCliente(id) {
    this.clienteService.cargarClientePorCedula(id).subscribe(resp => {
      this.cliente = resp;
    });
  }

  //Cargar todos los clientes
  CargarClientes() {
    this.clienteService.cargarClientes(this.empresa).subscribe(({ clientes }) => {
      this.options = [];
      for (let index = 0; index < clientes.length; index++) {
        this.options.push(clientes[index].ruccicliente);
      }
    });
  }

  //Filtro para ir buscando la cedula
  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();
    this.CargarClientes();
    if (value == '') {
      this.options = [];
    }
    return this.options.filter(option => option.toLowerCase().indexOf(filterValue) === 0);
  }

  //Metodo para cargar los datos del cliente 
  CargarDatosCliente(id) {
    this.CargarCliente(id);
  }
