import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GLOBAL } from './global';
import { Empresa } from '../models/empresa.model';
import { CargarEmpresa } from '../interfaces/cargar.interface';
import { CrearEmpresa } from '../interfaces/crear-nuevaempresa.interface';




@Injectable({
  providedIn: 'root'
})
export class EmpresaService {

  constructor(private http: HttpClient) { 
    this.url=GLOBAL.url;
  }

  public empresa: Empresa[] = [];

  private url: string;

  //Servicio para devolver empresas
  DevolverEmpresa(idEmpresa){
    const urlService = `${this.url}empresas/${idEmpresa}`;
    return this.http.get<CargarEmpresa>(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Servicio para devolver una empresa unica
  DevolverEmpresaUnica(idEmpresa){
    const urlService = `${this.url}empresaUnica/${idEmpresa}`;
    return this.http.get<Empresa>(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

//Servicio para crear nueva empresa

crearEmpresa(formData: CrearEmpresa){
  const urlService = `${this.url}empresa`;
  return this.http.post(urlService, formData, {
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
}

}
