import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GLOBAL } from './global';
import { Familia } from '../models/familias.model';
import { CargarFamilias } from '../interfaces/cargar.interface';
import { CrearFamilia } from '../interfaces/crear-nuevafamilia.interface';




@Injectable({
  providedIn: 'root'
})
export class FamiliasService {

  private url: string;
  public familia: Familia[] = [];

  constructor(private http: HttpClient) { this.url = GLOBAL.url; }

  CargarFamilias(idempresa: string) {
    const urlService = `${this.url}parfamilias/${idempresa}`
    return this.http.get<CargarFamilias>(urlService, {
      headers: {
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token'),
      }
    });
  }

  //Servicio para devolver impuestos especifico
  DevolverFamilias(idEmpresa) {
    const urlService = `${this.url}parfamilias/${idEmpresa}`;
    return this.http.get<CargarFamilias>(urlService, {
      headers: {
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Nuevos cambios
  BorrarFamilia(idFamilia: string) {
    const urlService = `${this.url}familia/delete/${idFamilia}`;
    return this.http.delete(urlService, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Servicio para guardar la familia  

crearFamilia(formData: CrearFamilia){
  const urlService = `${this.url}parfamilia`;
  return this.http.post(urlService, formData, {
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
  }

  cargarFamilia(idfamilia: string){
    const urlService = `${this.url}familia/${idfamilia}`;
    console.log(urlService);
    return this.http.get<Familia>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }
  
  actualizarFamilia(idfamilia: string, nuevoDatos: Familia){
    const urlService = `${this.url}parfamilia/${idfamilia}`;
    return this.http.put(urlService, nuevoDatos,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

}


