import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Unidad } from 'src/app/models/unidad.model';
import Swal from 'sweetalert2';
import { UnidadService } from '../../services/unidad.service';


@Component({
  selector: 'app-unidad',
  templateUrl: './unidad.component.html',
  styles: [
  ]
})
export class UnidadComponent implements OnInit {

  public unidades: Unidad[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");

  constructor(private unidadServices: UnidadService,
    private router: Router) { }

  ngOnInit(): void {
    console.log("Iniciando");
    this.CargarUnidades();
  }

  CargarUnidades(){
    //console.log("object2");
    this.cargando = true;
    this.unidadServices.cargarUnidades(this.empresa).subscribe( ({parunidad}) => {
      this.unidades = parunidad;
      this.cargando = false;
      console.log(parunidad);
    });
  }

  Borrar(parunidad: Unidad){
    Swal.fire({
      title: 'Esta seguro de borrar la unidad?',
      text: `Unidad: ${parunidad.idunidad}`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then(result => {
      if(result.value){
              this.unidadServices.BorrarUnidad(parunidad.idunidad).subscribe(resp => {
                this.CargarUnidades();
                Swal.fire('Unidad Borrada',`${parunidad.idunidad} fue eliminado correctamente`,'success');
              });
      }
    })
  }

}
