import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { FormasPagoService } from 'src/app/services/formas-pago.service';
import Swal from 'sweetalert2';
import { formaPago } from '../../../models/formaspago.model';
import { CargarFormasPagos } from '../../../interfaces/cargar-formas.interface';
import { CuentaContable } from 'src/app/models/conplancuentas';
import { CuentasService } from 'src/app/services/cuentas.service';

@Component({
  selector: 'app-actualizar',
  templateUrl: './actualizar.component.html',
  styles: [
  ]
})
export class ActualizarFormaPagoComponent implements OnInit {

  public formaPagoForm: FormGroup;
  empresa: string = localStorage.getItem('idemp');
  public formasPago: formaPago;
  public formaSubido: FormGroup;
  private searchResultSource = new BehaviorSubject<string>('');
  public contadorDeClientes: string = '';
  private codigoFormaDePago: string = '';
  public cuentasContables: CuentaContable[] = [];

  setSearchResults(_searchResult: string): void {
    this.searchResultSource.next(_searchResult);
  }

  constructor(
    private fb: FormBuilder,
    private formasPagoService: FormasPagoService,
    private cuentasService: CuentasService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) {}

  crearFormulario = () => {
    this.formaPagoForm = this.fb.group({
      idempresa: [this.empresa],
      idformapago: ['', Validators.required],
      nomformapago: ['', Validators.required],
      idcuenta: ['', Validators.required],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      idsucursal: ['SUC01'],
      activo: [true],
    });
  }

  CargarCuentas(idempres: string){
    this.cuentasService.CargarCuentas(this.empresa).subscribe(({contplancuentas})=>{
      this.cuentasContables = contplancuentas;
    });
  }

  ngOnInit(): void {
    this.crearFormulario();
    this.CargarFormasPago();
    this.CargarCuentas(this.empresa);
  }

  CargarFormasPago() {
    this.activatedRoute.params.subscribe(({ id }) => {
      console.log(id);
      this.codigoFormaDePago = id;
      console.log(this.codigoFormaDePago);
    });
    this.formasPagoService.cargarFormaPago(this.codigoFormaDePago).subscribe((resp) => {
      console.log(resp);
      const {
        idempresa,
        idformapago,
        nomformapago,
        idcuenta
      } = resp;
      const crepor = 1;
      const modpor = 1;
      const idsucursal = 'SUC01';
      const activo = true;
      this.formasPago = resp;
      this.formaPagoForm.setValue({
        idempresa,
        idformapago,
        nomformapago,
        idcuenta,
        crepor,
        modpor,
        idsucursal,
        activo,
      });
      return resp;
    });
  }

  guardarFormaDePago() {
    console.log('Guardar');
    this.formaSubido = this.formaPagoForm.value;
    const datos = {
      ...this.formaPagoForm.value
    } 
    console.log(datos);
    this.formasPagoService
      .actualizarFormasPago(
        datos.idformapago,
        datos
      )
      .subscribe(
        (resp) => {
          console.log(resp);
          Swal.fire('Actualizado', this.formaPagoForm.value['nomformapago'] + 'Forma de pago actualizada correctamente', 'success');
          this.router.navigateByUrl('dashboard/formasPago');
        },
        (err) => {
          Swal.fire(
            'Error',
            'No se ha podido actualizar la forma de pago intente mas tarde',
            'error'
          );
          console.log(err);
        }
      );
  }

  regresar() {
    this.router.navigateByUrl('dashboard/formasPago');
  }



}
