  //Funcion para cargar las formas de pago existentes de la empresa
  CargarFormasDePago(empresa) {
    var i = 0;
    this.formasPagoService
      .cargarFormasPago(empresa)
      .subscribe(({ parformapago }) => {
        for (let index = 0; index < parformapago.length; index++) {
          this.formas = {
            id: index,
            name: parformapago[index].nomformapago,
            idformapago: parformapago[index].idformapago,
          };
          this.estadosFormas.push(this.formas);
        }
        this.formasPagos = parformapago;
      });
  }

