  //DYR 09-06-2021.- Funcion para cargar las lineas a la transaccion
  CargarLineaTransacion(id, nombre, precio, impuesto, cantidad) {
    this.impuestosService
      .DevolverImpuestos(this.empresa)
      .subscribe(({ parimpuesto }) => {
        for (let index = 0; index < parimpuesto.length; index++) {
          if (parimpuesto[index].idimpuesto === impuesto) {
            if (parimpuesto[index].idimpuesto === 'IM1') {
              this.transactions.push({
                idempresa: this.empresa,
                idsucursal: 'SUC01',
                secmovdet: 0,
                secmovcab: 0,
                idproducto: id,
                nomproducto: nombre,
                cantidad: cantidad,
                precio: precio,
                subsindesc: precio * cantidad,
                porcdescuento: 0,
                descuento: 0,
                subtotal: precio * cantidad,
                subtotaliva0: precio * cantidad,
                subtotaliva12: 0,
                iva0: 0,
                iva12: 0,
                total: precio * cantidad,
                estado: true,
                crepor: 1,
                modpor: 1,
                createdAt: '',
                updatedAt: '',
              });
              this.subtotal0 = this.subtotal0 + precio * cantidad;
              this.subtotal = this.subtotal + precio * cantidad;
            } else if (parimpuesto[index].idimpuesto === 'IM2') {
              this.transactions.push({
                idempresa: this.empresa,
                idsucursal: 'SUC01',
                secmovdet: 0,
                secmovcab: 0,
                idproducto: id,
                nomproducto: nombre,
                cantidad: cantidad,
                precio: precio,
                subsindesc: precio * cantidad,
                porcdescuento: 0,
                descuento: 0,
                subtotal: precio * cantidad,
                subtotaliva0: 0,
                subtotaliva12: precio * cantidad,
                iva0: 0,
                iva12: precio * cantidad * 0.12,
                total: precio * cantidad + precio * cantidad * 0.12,
                estado: true,
                crepor: 1,
                modpor: 1,
                createdAt: '',
                updatedAt: '',
              });
              this.subtotal12 = this.subtotal12 + precio * cantidad;
              this.subtotal = this.subtotal + precio * cantidad;
              this.iva = this.subtotal12 * 0.12;
            }
          }
          this.total = this.subtotal + this.iva;
        }
      });
    this.cantidad = 1;
    this.precioEditado = 0;
  }

  //Funcion que recibe el id de la forma de pago y la agrega a la lista formasPagosAgregadas que son las que van a la factura
  AgregarFormaDePago(cantidad) {
    if (cantidad > 0) {
      var condicion = 1;
      var valorConDecimales = parseFloat(cantidad).toFixed(2);
      if (this.formaSeleccionada !== undefined) {
        for (let index = 0; index < this.detallesCobro.length; index++) {
          const element = this.detallesCobro[index];
          if (element.id === this.formaSeleccionada) {
            condicion = 0;
          }
        }
        if (condicion !== 0) {
          for (let index = 0; index < this.formasPagos.length; index++) {
            const element = this.formasPagos[index];
            if (element.idformapago === this.formaSeleccionada) {
              //console.log(this.formasPagos[index]);
              this.formaSeleccionada = {
                id: element.idformapago,
                nomformapago: this.formasPagos[index].nomformapago,
                valor: valorConDecimales,
              };
              this.detallesCobro.push(this.formaSeleccionada);
              this.importe =
                parseFloat(this.importe.toFixed(2)) +
                parseFloat(parseFloat(cantidad).toFixed(2));
            }
          }
        }
      }
    } else {
      Swal.fire({
        title: 'Error',
        text: 'La cantidad no puede ser menor a 0',
        icon: 'error',
      });
    }
  }

