  //Metodo para transformar secuencial de tabla idserie en 9 cifras segun esquema del numero de factura
  nueveCifras(number) {
    var numberOutput = Math.abs(number); /* Valor absoluto del nmero */
    var length = number.toString().length; /* Largo del nmero */
    var zero = '0'; /* String de cero */
    if (9 <= length) {
      if (number < 0) {
        return '-' + numberOutput.toString();
      } else {
        return numberOutput.toString();
      }
    } else {
      if (number < 0) {
        return '-' + zero.repeat(9 - length) + numberOutput.toString();
      } else {
        return zero.repeat(9 - length) + numberOutput.toString();
      }
    }
  }

