  //Funcion para borrar un producto de la lista
  Actualizar(numero) {

    this.subtotal0 = this.subtotal0 - this.transactions[numero].subtotaliva0;
    this.subtotal12 = this.subtotal12 - this.transactions[numero].subtotaliva12;
    this.subtotal = this.subtotal - this.transactions[numero].subtotal;
    this.iva = this.iva - this.transactions[numero].iva12;
    this.total = this.total - this.transactions[numero].total;
    if (this.subtotal0 == -0.0) {
      this.subtotal0 = 0;
    } else if (this.subtotal12 == -0.0) {
      this.subtotal12 = 0;
    } else if (this.subtotal == -0.0) {
      this.subtotal = 0;
    } else if (this.iva == -0.0) {
      this.iva = 0;
    } else if (this.total == -0.0) {
      this.total = 0;
    }
    this.transactions.splice(numero, 1);

  }

