  //Metodo para cargar cliente recibe la cedula del cliente que ingresa el cajero
  CargarCliente(id) {
    this.clienteService.cargarClientePorCedula(id).subscribe((resp) => {
      this.cliente = resp;
    });
  }

  //Cargar todos los clientes
  CargarClientes() {
    this.clienteService
      .cargarClientes(this.empresa)
      .subscribe(({ clientes }) => {
        this.options = [];
        for (let index = 0; index < clientes.length; index++) {
          this.options.push(clientes[index].ruccicliente);
        }
      });
  }

  //Filtro para ir buscando la cedula
  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();
    this.CargarClientes();
    if (value == '') {
      this.options = [];
    }
    return this.options.filter(
      (option) => option.toLowerCase().indexOf(filterValue) === 0
    );
  }

  //Metodo para cargar los datos del cliente
  CargarDatosCliente(id) {
    console.log(this.transactions.length);
    if (this.transactions.length == 0) {
      this.CargarCliente(id);
    } else {
      Swal.fire({
        title: 'Error',
        text: 'Para elegir otro usuario debe borrar los productos o cancelar la factura',
        icon: 'error',
      });
    }
  }

  //Funcion para quitar forma de pago
  QuitarFormaPago(id, idformapago, valor) {
    this.importe = this.importe - valor;
    for (let index = 0; index < this.estadosFormas.length; index++) {
      if (this.estadosFormas[index].id === idformapago) {
        this.formas = {
          id: idformapago,
          estado: 'listo',
        };
        this.estadosFormas[index] = this.formas;
      }
    }
    this.detallesCobro.splice(id, 1);
  }
