import { Injectable } from '@angular/core';
import { GLOBAL } from './global';
import { CabeceraFactura } from '../models/cabecerafactura.model';
import { HttpClient } from '@angular/common/http';
import { CargarFacturasCabeceras, IngresarFacturasCabeceras } from '../interfaces/cargar.interface';
import { DetalleFactura } from '../models/detallefactura.model';

@Injectable({
  providedIn: 'root'
})
export class FacturasService {

  constructor(private http: HttpClient) {
    this.url = GLOBAL.url;
  }

  public proformas: CabeceraFactura[] = [];

  private url: string;

  //localhost:8010/api/productos/EMP01

  facturaElectronicaUnica(id: string) {
    const urlService = `${this.url}cabmovfacs/${id}`;
    return this.http.get<CargarFacturasCabeceras>(urlService, {
      headers: {
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  cargarFacturas(empresa: string) {
    const urlService = `${this.url}cabmovfacs/${empresa}`;
    return this.http.get<CargarFacturasCabeceras>(urlService, {
      headers: {
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  actualizarFacturas(proforma: string, nuevoDatos: any) {
    const urlService = `${this.url}cabmovfac/${proforma}`;
    return this.http.put(urlService, nuevoDatos, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  actualizarFacturas2(proforma: string, nuevoDatos: any) {
    const urlService = `${this.url}cabmovfac/${proforma}`;
    return this.http.put(urlService, nuevoDatos, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  crearFacturas(factura: CabeceraFactura) {
    const urlService = `${this.url}cabmovfac`;
    return this.http.post<IngresarFacturasCabeceras>(urlService, factura, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  imprimirXML(id) {
    const urlService = `${this.url}descargarXML/${id}`;
    return this.http.get(urlService, {
      headers: {
        'Content-Type': 'Application/xml',
      }
    });
  }

  agregarDetalle(detalle: DetalleFactura) {
    const urlService = `${this.url}detmovimiento`;
    return this.http.post(urlService, detalle, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }
}
