import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CuentaContable } from 'src/app/models/conplancuentas';
import { CuentasService } from 'src/app/services/cuentas.service';
import { FamiliasService } from 'src/app/services/familias.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-actualizar-familia',
  templateUrl: './actualizar-familia.component.html',
  styles: [
  ]
})
export class ActualizarFamiliaComponent implements OnInit {

  public familiaForm: FormGroup;
  public familiaSubida: FormGroup;
  public cuentasContables: CuentaContable[] = [];
  private codigoFamilia: string = '';

  constructor(private fb: FormBuilder,
    private cuentasService: CuentasService,
    private familiasService: FamiliasService,
    private router: Router,
    private activatedRoute: ActivatedRoute) 
    { 
      this.CargarCuentas(this.empresa);
    }

    CargarCuentas(idempres: string){
      this.cuentasService.CargarCuentas(this.empresa).subscribe(({contplancuentas})=>{
        this.cuentasContables = contplancuentas;
      });
    }


  ngOnInit(){ this.crearFormulario();
  this.cargarFamilia();}


  //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
  empresa: string = localStorage.getItem('idemp');

  crearFormulario = () => {
    this.familiaForm = this.fb.group({
      idempresa: [this.empresa],
      idfamilia: ['', Validators.required],
      nomfamilia: ['', Validators.required],
      idcuenta: ['', Validators.required],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      idsucursal: ['SUC01'],
      activo: [true],
    });
  }

  cargarFamilia() {
    this.activatedRoute.params.subscribe(({ id }) => {
      this.codigoFamilia = id;
      console.log(this.codigoFamilia);
    });
    this.familiasService.cargarFamilia(this.codigoFamilia).subscribe((resp) => {
      console.log(resp);
      const {
        idempresa,
        idsucursal,
        idfamilia,
        nomfamilia, 
        idcuenta,
        crepor,
        activo,
      } = resp;
      const modpor = [localStorage.getItem('idcli')];
      this.familiaForm.setValue({
        idempresa,
        idsucursal,
        idfamilia,
        nomfamilia, 
        idcuenta,
        crepor,
        modpor,
        activo,
      });
      return resp;
    });
  }


  actualizarFamilia() {
    console.log('Guardar');
    this.familiaSubida = this.familiaForm.value;
    const datos = {
      ...this.familiaForm.value
    } 
    console.log(datos);
    this.familiasService
      .actualizarFamilia(
        datos.idfamilia,
        datos
      )
      .subscribe(
        (resp) => {
          console.log(resp);
          Swal.fire('Actualizado ', this.familiaForm.value['idfamilia'] + ' familia actualizada correctamente', 'success');
          this.router.navigateByUrl('dashboard/familia');
        },
        (err) => {
          Swal.fire(
            'Error',
            'No se ha podido actualizar la familia intente mas tarde',
            'error'
          );
          console.log(err);
        }
      );
  }

  regresar() {
    this.router.navigateByUrl('dashboard/familia');
  }


}
