import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Impuesto } from '../models/impuesto.model';
import { GLOBAL } from './global';
import { CargarImpuestos } from '../interfaces/cargar.interface';
import { CrearImpuesto } from '../interfaces/crear-nuevoimpuesto.interface';


@Injectable({
  providedIn: 'root'
})
export class ImpuestosService {

  get headers(){
    return{
      header:{
        'Content-Type': 'Aplication/json'
      }
    }
  }

  constructor(private http: HttpClient) { 
    this.url=GLOBAL.url;
  }

  public impuesto: Impuesto[] = [];

  private url: string;

  //Servicio para devolver todos los impuestos por empresa
  DevolverImpuestos(idEmpresa){
    const urlService = `${this.url}parimpuestos/${idEmpresa}`;
    return this.http.get<CargarImpuestos>(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  BorrarImpuesto(idImpuesto: string){
    const urlService = `${this.url}parimpuesto/delete/${idImpuesto}`; 
    return this.http.delete(urlService,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Servicio para guardar la sucursal  

crearImpuesto(formData: CrearImpuesto){
  const urlService = `${this.url}parimpuesto`;
  return this.http.post(urlService, formData, {
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
  }

  cargarImpuesto(idimpuesto: string){
    const urlService = `${this.url}parimpuesto/${idimpuesto}`;
    console.log(urlService);
    return this.http.get<Impuesto>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }
  
  actualizarImpuesto(idimpuesto: string, nuevoDatos: Impuesto){
    const urlService = `${this.url}parimpuesto/${idimpuesto}`;
    return this.http.put(urlService, nuevoDatos,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }
  
}
