import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GLOBAL } from './global';

@Injectable({
  providedIn: 'root'
})
export class ReportesProformasService {

  public url: string;

  constructor(private http: HttpClient) { 
    this.url = GLOBAL.url;
  }

  ReporteProformasFechas(empresa: string, fechaini: Date, fechafin: Date, estado: string){
    console.log(empresa,fechaini,fechafin,estado);
    const urlService = `${this.url}reporproforma/${empresa}/${fechaini}/${fechafin}/${estado}`;
    return this.http.get(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }
}
