import { Component, OnInit } from '@angular/core';
import { cabcobro } from 'src/app/models/cabcobro.model';
import { CobrosService } from 'src/app/services/cobros.service';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { DetallecobrosService } from 'src/app/services/detallecobros.service';
import { detcobro } from 'src/app/models/detcobro.model';

@Component({
  selector: 'app-cobros',
  templateUrl: './cobros.component.html',
  styles: [
  ]
})
export class CobrosComponent implements OnInit {

  constructor(private cobrosServices: CobrosService,
    private detcobrosServices: DetallecobrosService,
    private modal: NgbModal,
    ) { }

  public cobros: cabcobro[] = [];
  public detalles: detcobro[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");

  ngOnInit(): void {
    console.log("Iniciando");
    this.CargarCobros();
  }

  CargarCobros(){
    //console.log("object2");
    this.cargando = true;
    this.cobrosServices.DevolverCabcobros(this.empresa).subscribe( ({cabcobro}) => {
      this.cobros = cabcobro;
      this.cargando = false;
    });
  }

  DetalleDePago(contenido,secuencialRecibido) {
    console.log(secuencialRecibido);
    this.CargarDetalleCobros(secuencialRecibido);
        this.modal.open(contenido, {
          size: 'xl',
          centered: true,
          scrollable: true,
        });
      }   

  CargarDetalleCobros(secuencial){
       this.detcobrosServices.DevolverDetcobros(secuencial,this.empresa).subscribe( ({detcobro}) => {
          this.detalles = detcobro;
        });
      }

}
