import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';
import { FormasPagoService } from '../../../services/formas-pago.service';
import { formaPago } from '../../../models/formaspago.model';
import { BehaviorSubject } from 'rxjs';
import { CrearFormaPago } from '../../../interfaces/crear-nuevaformapago.interface';
import { CuentasService } from '../../../services/cuentas.service';
import { CuentaContable } from 'src/app/models/conplancuentas';

@Component({
  selector: 'app-nuevo',
  templateUrl: './nuevo.component.html',
  styles: [
  ]
})
export class NuevoFormasPagoComponent implements OnInit {

  public formaPagoForm: FormGroup;
  empresa: string = localStorage.getItem('idemp');
  public formasPagos: formaPago[] = [];
  public cuentasContables: CuentaContable[] = [];
  public formaPagoSubido: formaPago[] = [];
  private searchResultSource = new BehaviorSubject<string>('');

  setSearchResults(_searchResult: string): void {
    this.searchResultSource.next(_searchResult);
  }

  constructor(
    private fb: FormBuilder,
    private formasPagoService: FormasPagoService,
    private cuentasService: CuentasService,
    private router: Router
  ) { 
    this.CargarFormaPago(); 
    this.CargarCuentas(this.empresa);
  }

  crearFormulario = () => {
    this.formaPagoForm = this.fb.group({
      idempresa: [this.empresa],
      idformapago: ['', Validators.required],
      nomformapago: ['', Validators.required],
      idcuenta: ['', Validators.required],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      idsucursal: ['SUC01'],
      activo: [true],
    });
  }

  CargarCuentas(idempres: string){
    this.cuentasService.CargarCuentas(this.empresa).subscribe(({contplancuentas})=>{
      this.cuentasContables = contplancuentas;
    });
  }

  ngOnInit(): void {
    this.crearFormulario();
  }

  CargarFormaPago() {
    this.formasPagoService
      .cargarFormasPago(this.empresa)
      .subscribe(({ parformapago }) => {
        this.formasPagos = parformapago;
      });
  }

  public ClienteFinal: formaPago;

  guardarFormaPago() {
    this.formasPagoService.crearFormaPago(this.formaPagoForm.value).subscribe(resp => {
      console.log(resp);
      Swal.fire('Creada','Nueva forma de pago creada correctamente','success');
      this.router.navigateByUrl('dashboard/formasPago');
    }, (err) => {Swal.fire('Error','No se ha podido ingresar la forma de pago intente mas tarde','error');});
  }

  regresar(){
    this.router.navigateByUrl('dashboard/formasPago');
  }

}
