import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { UnidadService } from 'src/app/services/unidad.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-actualizar-unidad',
  templateUrl: './actualizar-unidad.component.html',
  styles: [
  ]
})
export class ActualizarUnidadComponent implements OnInit {

  public unidadForm: FormGroup;
  public unidadSubida: FormGroup;
  private codigoUnidad: string = '';

  constructor(private fb: FormBuilder,
    private unidadService: UnidadService,
    private router: Router,
    private activatedRoute: ActivatedRoute) { }

  ngOnInit(){ this.crearFormulario();
  this.cargarUnidad();}

    //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
    empresa: string = localStorage.getItem('idemp');

    crearFormulario = () => {
      this.unidadForm = this.fb.group({
        idempresa: [this.empresa],
        idsucursal: ['SUC01'],
        idunidad: ['', Validators.required],
        nomunidad: ['', Validators.required],
        crepor: [localStorage.getItem('idcli')],
        modpor: [0],
        activo: [true],
      });
  
    }
  
    cargarUnidad() {
      this.activatedRoute.params.subscribe(({ id }) => {
        this.codigoUnidad = id;
        console.log(this.codigoUnidad);
      });
      this.unidadService.cargarUnidad(this.codigoUnidad).subscribe((resp) => {
        console.log(resp);
        const {
          idempresa,
          idsucursal,
          idunidad,
          nomunidad,
          crepor,
          activo
        } = resp;
        const modpor = [localStorage.getItem('idcli')];
        this.unidadForm.setValue({
          idempresa,
          idsucursal,
          idunidad,
          nomunidad,
          crepor,
          modpor,
          activo,
        });
        return resp;
      });
    }
  
    actualizarUnidad() {
      console.log('Guardar');
      this.unidadSubida = this.unidadForm.value;
      const datos = {
        ...this.unidadForm.value
      } 
      console.log(datos);
      this.unidadService
        .actualizarUnidad(
          datos.idunidad,
          datos
        )
        .subscribe(
          (resp) => {
            console.log(resp);
            Swal.fire('Actualizado ', this.unidadForm.value['idunidad'] + ' unidad actualizada correctamente', 'success');
            this.router.navigateByUrl('dashboard/unidades');
          },
          (err) => {
            Swal.fire(
              'Error',
              'No se ha podido actualizar la unidad intente mas tarde',
              'error'
            );
            console.log(err);
          }
        );
    }
  
    regresar() {
      this.router.navigateByUrl('dashboard/unidades');
    }
  
}
