import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Planestructura } from 'src/app/models/estrucuentas.model';
import { EstrucuentasService } from 'src/app/services/estrucuentas.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-actualizar-estrucuentas',
  templateUrl: './actualizar-estrucuentas.component.html',
  styles: [
  ]
})
export class ActualizarEstrucuentasComponent implements OnInit {

  public estrucuentasForm: FormGroup;
  private codigoEstrucuentas: string = '';
  private planestructura: Planestructura;
  public planestructuraSubido: FormGroup;


  constructor(private fb: FormBuilder,
    private estrucuentasService: EstrucuentasService,
    private router: Router,
    private activatedRoute: ActivatedRoute) 
    {}

  ngOnInit(){ 
    this.crearFormulario();
    this.CargarEstrucuentas();
  }

  //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
  empresa: string = localStorage.getItem('idemp');

  crearFormulario = () => {
    this.estrucuentasForm = this.fb.group({
      idempresa: [this.empresa],
      estr1cuenta:[0],
      estr2cuenta:[0],
      estr3cuenta:[0],
      estr4cuenta:[0],
      estr5cuenta:[0],
      estr6cuenta:[0],
      sepestrucuenta: ['.', Validators.required],
      longcuenta:[0],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
    });

  }

  CargarEstrucuentas() {
    this.activatedRoute.params.subscribe(({ id }) => {
      this.codigoEstrucuentas = id;
    });
    this.estrucuentasService.cargarEstrucuentas(this.codigoEstrucuentas).subscribe((resp) => {
       const {
        idempresa,
        estr1cuenta,
        estr2cuenta,
        estr3cuenta,
        estr4cuenta,
        estr5cuenta,
        sepestrucuenta,
        longcuenta,
        crepor
      } = resp;
      const modpor = [localStorage.getItem('idcli')];
      const estr6cuenta=0;
      this.planestructura = resp;
      this.estrucuentasForm.setValue({
        idempresa,
        estr1cuenta,
        estr2cuenta,
        estr3cuenta,
        estr4cuenta,
        estr5cuenta,
        estr6cuenta,
        sepestrucuenta,
        longcuenta,
        crepor,
        modpor,
      });
      return resp;
    });
    
  }


  actualizarEstrucuentas() {
    this.planestructuraSubido = this.estrucuentasForm.value;
    const datos = {
      ...this.estrucuentasForm.value
    } 
    console.log(datos);
    this.estrucuentasService
      .actualizarEstrucuentas(
        datos.idempresa,
        datos
      )
      .subscribe(
        (resp) => {
          console.log(resp);
          Swal.fire('Actualizado ', this.estrucuentasForm.value['idempresa'] + ' estructura de plan actualizada correctamente', 'success');
          this.router.navigateByUrl('dashboard/estrucuentas');
        },
        (err) => {
          Swal.fire(
            'Error',
            'No se ha podido actualizar la estructura del plan intente mas tarde',
            'error'
          );
          console.log(err);
        }
      );
  }


  regresar() {
    this.router.navigateByUrl('dashboard/estrucuentas');
  }



}
