  //Funcion para quitar forma de pago
  QuitarFormaPago(id, idformapago, valor) {
    this.importe = this.importe - valor;
    for (let index = 0; index < this.estadosFormas.length; index++) {
      if (this.estadosFormas[index].id === idformapago) {
        this.formas = {
          id: idformapago,
          estado: 'listo'
        }
        this.estadosFormas[index] = this.formas;
      }
    }
    this.detallesCobro.splice(id, 1);
  }

  //Funcion que recibe el id de la forma de pago y la agrega a la lista formasPagosAgregadas que son las que van a la factura
  AgregarFormaDePago(cantidad) {
    if (cantidad > 0) {
      var condicionEfectivo = 0;
      var condicion = 1;
      var valorConDecimales = parseFloat(cantidad).toFixed(2);
      if (this.formaSeleccionada !== undefined) {
        for (let index = 0; index < this.detallesCobro.length; index++) {
          const element = this.detallesCobro[index];
          if (element.id === this.formaSeleccionada && element.id === 'EFECT') {
            condicionEfectivo++;
          } else if (element.id === this.formaSeleccionada) {
            condicion = 0;
          }
          if (condicionEfectivo === 2) {
            condicion = 0;
          }
        }
        if (condicion !== 0) {
          for (let index = 0; index < this.formasPagos.length; index++) {
            const element = this.formasPagos[index];
            if (element.idformapago === this.formaSeleccionada) {
              //console.log(this.formasPagos[index]);
              this.formaSeleccionada = {
                id: element.idformapago,
                nomformapago: this.formasPagos[index].nomformapago,
                valor: valorConDecimales
              };
              this.detallesCobro.push(this.formaSeleccionada);
              this.importe = parseFloat(this.importe.toFixed(2)) + parseFloat((parseFloat(cantidad)).toFixed(2));
            }
          }
        }
      }
    } else {
      Swal.fire({
        title: "Error",
        text: "La cantidad no puede ser menor a 0",
        icon: "error"
      });
    }
  }

  //Filtro para ir buscando la cedula
  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();
    this.CargarClientes();
    if (value == '') {
      this.options = [];
    }
    return this.options.filter(option => option.toLowerCase().indexOf(filterValue) === 0);
  }

