  //Funcion para borrar un producto de la lista
  Actualizar(numero) {
    this.subtotal0 = this.subtotal0 - this.transactions[numero].subtotaliva0;
    this.subtotal12 = this.subtotal12 - this.transactions[numero].subtotaliva12;
    this.subtotal = this.subtotal - this.transactions[numero].subtotal;
    this.iva = this.iva - this.transactions[numero].iva12;
    this.total = this.total - this.transactions[numero].total;
    if (this.subtotal0 == -0.00) {
      this.subtotal0 = 0;
    } else if (this.subtotal12 == -0.00) {
      this.subtotal12 = 0;
    }
    else if (this.subtotal == -0.00) {
      this.subtotal = 0;
    }
    else if (this.iva == -0.00) {
      this.iva = 0;
    }
    else if (this.total == -0.00) {
      this.total = 0;
    }
    this.transactions.splice(numero, 1);
  }

  //Funcion para encerar las proformas
  EncerarProforma() {
    this.router.navigateByUrl('dashboard/facturar');
  }

  EscogerFormaDePago(contenido) {
    if (this.cliente.tipocliente == 'Credito') {
      this.FacturarClientesCredito();
    } else {
      if (this.total == 0.00 || this.total == -0.00 || this.total.toFixed(2) == '0.00' || this.total.toFixed(2) == '-0.00') {
        Swal.fire({
          title: "Error",
          text: "Factura incompleta no exiten productos",
          icon: "error"
        });
      } else {
        if (this.cliente.idcliente == '') {
          Swal.fire({
            title: "Error",
            text: "Factura incompleta no exite cliente",
            icon: "error"
          });
        } else {
          this.modal.open(contenido, { size: 'xl', centered: true, scrollable: true });
        }
      }
    }
  }

  //Metodo para transformar secuencial de tabla idserie en 9 cifras segun esquema del numero de factura
  nueveCifras(number) {
    var numberOutput = Math.abs(number); /* Valor absoluto del nmero */
    var length = number.toString().length; /* Largo del nmero */
    var zero = "0"; /* String de cero */
    if (9 <= length) {
      if (number < 0) {
        return ("-" + numberOutput.toString());
      } else {
        return numberOutput.toString();
      }
    } else {
      if (number < 0) {
        return ("-" + (zero.repeat(9 - length)) + numberOutput.toString());
      } else {
        return ((zero.repeat(9 - length)) + numberOutput.toString());
      }
    }
  }

  //Funcion del boton que agrega productos en la factura aqui se recibe el ID del producto
  //En este metodo tambien se acumulan las variables de subtotal, iva y total
  AgregarProducto(id, nombre, precio, impuesto, cantidad, precioEditado) {
    if (cantidad < 0 || ((cantidad % 1) != 0)) {
      Swal.fire('Error', 'La cantidad tiene que ser mayor a 0 y un numero entero', 'error');
      this.cantidad = 1;
    } else {
      if (precioEditado < 0) {
        Swal.fire('Error', 'El precio no puede ser menor a cero', 'error');
        this.precioEditado = 0;
      } else {
        if (cantidad == '' || cantidad == 0) {
          Swal.fire('Error', 'No puede ingresar productos con cantidad 0', 'error');
        } else {
          if (precioEditado != 0) {
            precio = precioEditado;
          }
          this.precioEspecialService.DevolverPrecioClientes(this.empresa).subscribe(({ precioclientes }) => {
            for (let index = 0; index < precioclientes.length; index++) {
              if (precioclientes[index].idproducto === id && (precioclientes[index].idcliente === this.cliente.idcliente || precioclientes[index].idcliente === this.cliente.idcliente_pertenece)) {
                precio = precioclientes[index].precio;
              }
            }
          });
          this.impuestosService.DevolverImpuestos(this.empresa).subscribe(({ parimpuesto }) => {
            for (let index = 0; index < parimpuesto.length; index++) {
              if (parimpuesto[index].idimpuesto === impuesto) {
                if (parimpuesto[index].idimpuesto === 'IM1') {
                  this.transactions.push({ idempresa: this.empresa, idsucursal: 'SUC01', secmovdet: 0, secmovcab: 0, idproducto: id, nomproducto: nombre, cantidad: cantidad, precio: precio, subsindesc: precio * cantidad, porcdescuento: 0, descuento: 0, subtotal: (precio * cantidad), subtotaliva0: (precio * cantidad), subtotaliva12: 0, iva0: 0, iva12: 0, total: precio * cantidad, estado: true, crepor: 1, modpor: 1, createdAt: '', updatedAt: '' });
                  this.subtotal0 = this.subtotal0 + (precio * cantidad);
                  this.subtotal = this.subtotal + (precio * cantidad);
                } else if (parimpuesto[index].idimpuesto === 'IM2') {
                  this.transactions.push({ idempresa: this.empresa, idsucursal: 'SUC01', secmovdet: 0, secmovcab: 0, idproducto: id, nomproducto: nombre, cantidad: cantidad, precio: precio, subsindesc: precio * cantidad, porcdescuento: 0, descuento: 0, subtotal: (precio * cantidad), subtotaliva0: 0, subtotaliva12: (precio * cantidad), iva0: 0, iva12: ((precio * cantidad) * 0.12), total: (precio * cantidad) + ((precio * cantidad) * 0.12), estado: true, crepor: 1, modpor: 1, createdAt: '', updatedAt: '' });
                  this.subtotal12 = this.subtotal12 + (precio * cantidad);
                  this.subtotal = this.subtotal + (precio * cantidad);
                  this.iva = this.subtotal12 * 0.12;
                }
              }
              this.total = this.subtotal + this.iva;
            }
          });
          this.cantidad = 1;
          this.precioEditado = 0;
        }
      }
    }
  }
