import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { GLOBAL } from "./global";
import { Cliente } from '../models/cliente.model';
import { CargarCliente } from '../interfaces/cargar.interface';
import { delay, tap } from 'rxjs/operators';
import { CrearCliente } from '../interfaces/crear-nuevocliente.interface';

@Injectable({
  providedIn: 'root'
})
export class ClientesService {


  get headers(){
    return{
      header:{
        'Content-Type': 'Aplication/json'
      }
    }
  }

  
  public clientes: Cliente[] = [];

  private url: string;

  constructor( private http: HttpClient ) { 
    this.url=GLOBAL.url;
  }

  cargarClientes(empresa: string){
    const urlService = `${this.url}clientes/${empresa}`;
    return this.http.get<CargarCliente>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  cargarClientesCredito(empresa: string){
    const urlService = `${this.url}clientescredito/${empresa}`;
    return this.http.get<CargarCliente>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  cargarCliente(idcliente: string){
    const urlService = `${this.url}cliente/${idcliente}`;
    return this.http.get<Cliente>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  cargarClientePorCedula(idcliente: string){
    const urlService = `${this.url}clienteporcedula/${idcliente}`;
    return this.http.get<Cliente>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  actualizarClientes(cliente: string, nuevoDatos: Cliente){
    const urlService = `${this.url}cliente/${cliente}`;
    return this.http.put(urlService, nuevoDatos,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  crearClientes( formData: CrearCliente ){
    const urlService = `${this.url}cliente`;
    return this.http.post(urlService, formData,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  borrarClientes(idcliente: string){
    const urlService = `${this.url}clientes/delete/${idcliente}`;
    return this.http.delete(urlService,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }
}
