import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { EstrucuentasService } from 'src/app/services/estrucuentas.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-nuevo-estrucuentas',
  templateUrl: './nuevo-estrucuentas.component.html',
  styles: [
  ]
})
export class NuevoEstrucuentasComponent implements OnInit {

  public estrucuentasForm: FormGroup;

  constructor(private fb: FormBuilder,
    private estrucuentasService: EstrucuentasService,
    private router: Router) { }

  ngOnInit(){ this.crearFormulario();}

  //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
  empresa: string = localStorage.getItem('idemp');

  crearFormulario = () => {
    this.estrucuentasForm = this.fb.group({
      idempresa: [this.empresa],
      estr1cuenta:[0],
      estr2cuenta:[0],
      estr3cuenta:[0],
      estr4cuenta:[0],
      estr5cuenta:[0],
      estr6cuenta:[0],
      sepestrucuenta: ['.', Validators.required],
      longcuenta:[0],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
    });

  }

  guardarEstrucuentas() {
    this.estrucuentasService.crearEstrucuentas(this.estrucuentasForm.value).subscribe(resp => {
      console.log(resp);
      Swal.fire('Creado','Nueva estrucntura de cuentas creada correctamente','success');
      this.router.navigateByUrl('dashboard/estrucuentas');
    }, (err) => {Swal.fire('Error','No se ha podido ingresar la estructura de cuentas intente mas tarde','error');});
  }

  regresar() {
    this.router.navigateByUrl('dashboard/estrucuentas');
  }




}
