import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CargarCuentas } from '../interfaces/cargar.interface';
import { GLOBAL } from './global';

@Injectable({
  providedIn: 'root'
})
export class CuentasService {

  private url: string;

  constructor(private http: HttpClient) { this.url=GLOBAL.url; }

  CargarCuentas(idempresa: string){
    const urlService = `${this.url}contplancuentasmov/${idempresa}`
    return this.http.get<CargarCuentas>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

}
