import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Sucursal } from '../models/sucursal.model';
import { GLOBAL } from './global';
import { CargarSucursales } from '../interfaces/cargar.interface';
import { CrearSucursal } from '../interfaces/crear-nuevasucursal.interface';


@Injectable({
  providedIn: 'root'
})
export class SucursalService {

  get headers(){
    return{
      header:{
        'Content-Type': 'Aplication/json'
      }
    }
  }

  constructor(private http: HttpClient) { 
    this.url=GLOBAL.url;
  }

  public sucursal: Sucursal[] = [];

  private url: string;

  //Servicio para devolver impuestos especifico
  DevolverSucursales(idEmpresa){
    const urlService = `${this.url}sucursales/${idEmpresa}`;
    return this.http.get<CargarSucursales>(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  BorrarSucursal(idSucursal: string){
    const urlService = `${this.url}sucursal/delete/${idSucursal}`; 
    return this.http.delete(urlService,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

//Servicio para guardar la sucursal  

crearSucursal(formData: CrearSucursal){
  const urlService = `${this.url}sucursal`;
  return this.http.post(urlService, formData, {
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
}

cargarSucursal(idsucursal: string){
  const urlService = `${this.url}sucursal/${idsucursal}`;
  return this.http.get<Sucursal>(urlService, {
    headers:{
      'Content-Type': 'Aplication/json',
      'Authorization': localStorage.getItem('token')
    }
  });
}

actualizarSucursal(sucursal: string, nuevoDatos: Sucursal){
  const urlService = `${this.url}sucursal/${sucursal}`;
  return this.http.put(urlService, nuevoDatos,{
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
}


}
