import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GLOBAL } from './global';
import { Contplancuentas } from '../models/contplancuentas.model';
import { CargarContplancuentas } from '../interfaces/cargar.interface';
import { CrearPlancuentas } from '../interfaces/crear-nuevoplancuentas.interface';


@Injectable({
  providedIn: 'root'
})
export class ContplancuentasService {

  constructor(private http: HttpClient) { 
    this.url=GLOBAL.url;
  }

  public contplancuentas: Contplancuentas[] = [];

  private url: string;

  //Servicio para devolver impuestos especifico
  DevolverCuentas(idEmpresa){
    const urlService = `${this.url}contplancuentas/${idEmpresa}`;
    return this.http.get<CargarContplancuentas>(urlService,{
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  BorrarCuentas(idCuenta: string){
    const urlService = `${this.url}contplancuentas/delete/${idCuenta}`; 
    return this.http.delete(urlService,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Servicio para guardar la sucursal  

crearPlancuentas(formData: CrearPlancuentas){
  const urlService = `${this.url}contplancuentas`;
  return this.http.post(urlService, formData, {
    headers:{
      'Authorization': localStorage.getItem('token')
    }
  });
  }

  cargarPlancuentas(idcuenta: string){
    const urlService = `${this.url}cuenta/${idcuenta}`;
    console.log(urlService);
    return this.http.get<Contplancuentas>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  
  }

  actualizarPlancuentas(idcuenta: string, nuevoDatos: Contplancuentas){
    const urlService = `${this.url}contplancuentas/${idcuenta}`;
    return this.http.put(urlService, nuevoDatos,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

}
