import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ClientesService } from '../../../services/clientes.service';
import { CargarCliente } from '../../../interfaces/cargar.interface';
import { Cliente } from 'src/app/models/cliente.model';
import { Usuarios } from 'src/app/models/usuariom.model';
import { BehaviorSubject } from 'rxjs';
import Swal from 'sweetalert2';
import { Router } from '@angular/router';
import { UsuariosService } from '../../../services/usuarios.service';
import { AuthService } from '../../../services/auth.service';

@Component({
  selector: 'app-nuevo',
  templateUrl: './nuevo.component.html',
  styles: [
  ]
})
export class NuevoComponent implements OnInit {

  public clienteForm: FormGroup;
  empresa: string = localStorage.getItem('idemp');
  public clientes: Cliente[] = [];
  public clienteSubido: Cliente[] = [];
  private searchResultSource = new BehaviorSubject<string>('');
  public contadorDeClientes: string = '';
  usuarios: Usuarios;

  setSearchResults(_searchResult: string): void {
    this.searchResultSource.next(_searchResult);
  }

  constructor(
    private fb: FormBuilder,
    private clienteService: ClientesService,
    private router: Router,
    private usuariosService: UsuariosService,
    private authService: AuthService
  ) {
    this.CargarCliente();
    this.CargarClientesParaPrecios();
  }

  crearFormulario = () => {
    this.clienteForm = this.fb.group({
      idempresa: [this.empresa],
      idcliente: [''],
      idencliente: ['Cedula', Validators.required],
      ruccicliente: ['', [Validators.required, Validators.maxLength(13)]],
      nomcliente: ['', Validators.required],
      dircliente: ['', Validators.required],
      telfcliente: ['', Validators.required],
      emailcliente: ['', [Validators.email, Validators.required]],
      crepor: [localStorage.getItem('idcli')],
      modpor: [0],
      idsucursal: ['SUC01'],
      activo: [true],
      idcliente_pertenece: [''],
      tipocliente: ['Contado', Validators.required]
    });
  }

  ngOnInit(): void {
    this.crearFormulario();
  }

  CargarCliente() {
    this.clienteService
      .cargarClientes(this.empresa)
      .subscribe(({ clientes }) => {
        this.clientes = clientes;
        var letra = 'CL';
        this.contadorDeClientes = '';
        var cadenaADividir = this.clientes[this.clientes.length - 1].idcliente;
        var arrayDeCadenas = cadenaADividir.split(letra);
        for (var i = 0; i < arrayDeCadenas.length; i++) {
          this.contadorDeClientes = this.contadorDeClientes + arrayDeCadenas[i];
        }
        this.contadorDeClientes = (
          parseInt(this.contadorDeClientes) + 1
        ).toString();
      });
  }

  public ClienteFinal: Cliente;

  validarCedula(cedula, tipoDocumento) {
    var cedulaValidada = this.clienteSubido['ruccicliente'].toString();
    if (tipoDocumento == 'Cedula') {
      if (cedulaValidada.length == 10) {
        this.clienteSubido = cedula;
        var numeroProvincia: number = parseInt(cedulaValidada[0] + cedulaValidada[1]);
        if (numeroProvincia < 0 || numeroProvincia > 24) {
          Swal.fire('Error', 'La provincia esta fuera del rango', 'error');
        } else if (cedulaValidada[2] < 6 && cedulaValidada[2] >= 0) {
          var resultado;
          var coeficiente = 2;
          var acumuladorResultado: number = 0;
          var decenaFinal = 0;
          var resultadoFinal;
          for (let i = 0; i <= 8; i++) {
            resultado = parseInt(cedulaValidada[i]) * coeficiente;
            if (coeficiente == 2) {
              coeficiente = 1
            } else {
              coeficiente = 2
            }
            if (resultado >= 10) {
              resultado = resultado - 9;
              acumuladorResultado = acumuladorResultado + resultado;
            } else {
              acumuladorResultado = acumuladorResultado + resultado;
            }
          }
          while (decenaFinal < acumuladorResultado) {
            decenaFinal = decenaFinal + 10;
          }
          resultadoFinal = decenaFinal - acumuladorResultado;
          if (resultadoFinal === parseInt(cedulaValidada[9])) {
            return true;
          } else {
            return false;
          }
        } else {
          return false;
        }
      } else {
        Swal.fire('Error', 'El tamao de la cedula no es correcto', 'error');
        return false;
      }
    } else if (tipoDocumento == 'RUC') {
      if (cedulaValidada.length == 13) {
        this.clienteSubido = cedula;
        var cedulaValidada = this.clienteSubido['ruccicliente'].toString();
        var numeroProvincia: number = parseInt(cedulaValidada[0] + cedulaValidada[1]);
        if (numeroProvincia < 0 || numeroProvincia > 24) {
          Swal.fire('Error', 'La provincia esta fuera del rango', 'error');
        } else if (cedulaValidada[2] < 6 && cedulaValidada[2] >= 0) {
          var resultado;
          var coeficiente = 2;
          var acumuladorResultado: number = 0;
          var decenaFinal = 0;
          var resultadoFinal;
          for (let i = 0; i <= 8; i++) {
            resultado = parseInt(cedulaValidada[i]) * coeficiente;
            if (coeficiente == 2) {
              coeficiente = 1
            } else {
              coeficiente = 2
            }
            if (resultado >= 10) {
              resultado = resultado - 9;
              acumuladorResultado = acumuladorResultado + resultado;
            } else {
              acumuladorResultado = acumuladorResultado + resultado;
            }
          }
          while (decenaFinal < acumuladorResultado) {
            decenaFinal = decenaFinal + 10;
          }
          resultadoFinal = decenaFinal - acumuladorResultado;
          if (resultadoFinal === parseInt(cedulaValidada[9])) {
            if ((cedulaValidada[10] == 0 && cedulaValidada[11] == 0 && cedulaValidada[12] == 1) || (cedulaValidada[10] == 0 && cedulaValidada[11] == 0 && cedulaValidada[12] == 2) || (cedulaValidada[10] == 0 && cedulaValidada[11] == 0 && cedulaValidada[12] == 3)) {
              return true;
            } else {
              return false;
            }
          } else {
            return false;
          }
        } else if (cedulaValidada[2] == 9) {
          var resultadoRUC: number = 0;
          var coeficienteJuridico = '432765432';
          for (let i = 0; i <= 8; i++) {
            resultadoRUC = resultadoRUC + (parseInt(cedulaValidada[i]) * parseInt(coeficienteJuridico[i]));
          }
          var residuo = resultadoRUC % 11;
          var valorFinalComparado = 11 - residuo;
          if (residuo == 0) {
            valorFinalComparado = 0;
          } else if (residuo == 1) {
            valorFinalComparado = 1;
          }
          if (valorFinalComparado == cedulaValidada[9]) {
            return true;
          } else {
            return false;
          }
        } else if (cedulaValidada[2] == 6) {
          var resultadoRUC: number = 0;
          var coeficientePublico = '32765432';
          for (let i = 0; i <= 8; i++) {
            resultadoRUC = resultadoRUC + (parseInt(cedulaValidada[i]) * parseInt(coeficientePublico[i]));
          }
          var residuo = resultadoRUC % 11;
          var valorFinalComparado = 11 - residuo;
          if (residuo == 0) {
            valorFinalComparado = 0;
          } else if (residuo == 1) {
            valorFinalComparado = 1;
          }
          if (valorFinalComparado == cedulaValidada[8]) {
            return true;
          } else {
            return false;
          }
        } else {
          return false;
        }
      } else {
        Swal.fire('Error', 'El tamao del ruc no es correcto', 'error');
        return false;
      }
    } else {
      return true;
    }
  }


  guardarCliente() {
    this.clienteSubido = this.clienteForm.value;
    var tipoDocumento = this.clienteSubido['idencliente'];
    var cedula = this.clienteSubido['ruccicliente'];
    var condicionExistencia = 0;
    if (this.validarCedula(this.clienteSubido, tipoDocumento) === true) {
      this.clienteService.cargarClientes(this.empresa).subscribe(({ clientes }) => {
        clientes.forEach(element => {
          if (element.ruccicliente === cedula) {
            condicionExistencia = 1;
          }
        });
        if (condicionExistencia === 1) {
          Swal.fire('Error', 'El cliente ya existe', 'error');
        } else {
          if (this.clienteForm.value['idcliente_pertenece'] == '') {
            this.clienteForm.value['idcliente_pertenece'] = null;
          }
          this.clienteService.crearClientes(this.clienteForm.value).subscribe(resp => {
            Swal.fire('Creado', 'Cliente ingresado correctamente', 'success');
            this.router.navigateByUrl('dashboard/clientes');
          }, (err) => { Swal.fire('Error', 'No se ha podido ingresar el cliente intente mas tarde', 'error'); });
        }
      })
    } else {
      Swal.fire('Error', 'La cdula o ruc no es vlida', 'error');
    }
  }

  //Funcion para redirigir al cajero a realizar la factura
  guardarClienteFacturar() {
    this.clienteSubido = this.clienteForm.value;
    var tipoDocumento = this.clienteSubido['idencliente'];
    var cedula = this.clienteSubido['ruccicliente'];
    var condicionExistencia = 0;
    if (this.validarCedula(this.clienteSubido, tipoDocumento) === true) {
      this.clienteService.cargarClientes(this.empresa).subscribe(({ clientes }) => {
        clientes.forEach(element => {
          if (element.ruccicliente === cedula) {
            condicionExistencia = 1;
          }
        });
        if (condicionExistencia == 1) {
          Swal.fire('Error', 'El cliente ya existe', 'error');
        } else {
          this.clienteSubido['idcliente'] = `CL${this.contadorDeClientes}`;
          if (this.clienteForm.value['idcliente_pertenece'] == '') {
            this.clienteForm.value['idcliente_pertenece'] = null;
          }
          this.clienteService.crearClientes(this.clienteForm.value).subscribe(resp => {
            Swal.fire('Creado', 'Cliente ingresado correctamente', 'success');
            this.router.navigateByUrl('dashboard/facturar');
          }, (err) => { Swal.fire(err); });
        }
      });
    } else {
      Swal.fire('Error', 'La cdula no es vlida', 'error');
    }
  }

  regresar() {
    this.router.navigateByUrl('dashboard/clientes');
  }

  //Cargar los clientes para relacionar con los precios especiales
  CargarClientesParaPrecios() {
    this.authService.ObtenerEmpresa().subscribe(resp => {
      this.clienteService.cargarClientesCredito(resp).subscribe(({ clientes }) => {
        this.clientes = clientes;
      });
    });
  }

}
