import { EventEmitter, Injectable, Output } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { UsuarioModel } from '../models/usuario.model';
import { GLOBAL } from './global';
import { catchError, map, tap } from "rxjs/operators";
import { Observable, of } from 'rxjs';
import { LoginForm } from '../interfaces/login-form.interface';
import { UsuarioSerieModel } from '../models/usuarioSerie.model';
import { CargarUsuSerie } from '../interfaces/cargar.interface';
import { Empresa } from '../models/empresa.model';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  // Trabajando con servidor de pruebas en localhost estas direcciones se deben cambiar por el servidor produccion

  //login
  //localhost:8010/api/login
  private url: string;
  constructor(private http: HttpClient) {
    this.url = GLOBAL.url;
  }

  public usuario: UsuarioModel;

  logout() {
    //BORRAR EL MENU
  }

  get role(): '1' | '2' {
    return this.usuario.idrol;
  }

  get idUsuario(): string {
    return this.usuario.usuario;
  }

  get id(): string {
    return this.usuario.idempresa;
  }

  ObtenerEmpresa(){
    const id = this.id;
    const token = localStorage.getItem('token') || '';
    const urlService = `${this.url}usuario/empresa/${id}`;
    return this.http.get(urlService,{
      headers: {
        'Authorization': token
      }, 'responseType': 'text'
    });
  }

  //Este devolver empresa devuelve un objeto de tipo empresa se hizo para corregir un error al deserializar el servicio
  ObtenerEmpresa2(){
    const id = this.id;
    const token = localStorage.getItem('token') || '';
    const urlService = `${this.url}usuario/empresa/${id}`;
    return this.http.get<Empresa>(urlService,{
      headers: {
        'Authorization': token
      }
    });
  }

  EncontrarSecuencial() {
    const id = this.idUsuario;
    const token = localStorage.getItem('token') || '';
    const urlService = `${this.url}secuencial`;
    return this.http.post<CargarUsuSerie>(urlService, {
      usuario: id
    }, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  validarToken(): Observable<boolean> {
    const token = localStorage.getItem('token') || '';
    const urlService = `${this.url}comprobar`;
    return this.http.get(urlService, {
      headers: {
        'Content-Type': 'Aplication/json',
        'Authorization': token
      }
    }).pipe(
      map((resp: any) => {
        //this.guardarLocalStorage2(resp.menu);
        //console.log(resp.verifiedJwt.body);
        const { usuario, idrol, idusuario
        } = resp.verifiedJwt.body;
        this.usuario = new UsuarioModel(usuario, idrol, idusuario);
        return true
      }),
      catchError(error => of(false))
    );
  }

  login(formData: LoginForm) {
    /*  const authData = {
       ...usuario,
       token: true
     } */

    return this.http.post(`${this.url}login`, formData).pipe(
      tap(
        (resp: any) => {
          this.guardarLocalStorage(resp.token, resp.menu);
          localStorage.setItem("idemp", resp.usuario.idempresa);
          localStorage.setItem("usu", resp.usuario.usuario);
          localStorage.setItem("idcli", resp.usuario.idusuario);
          localStorage.setItem("tipo", resp.usuario.tipo);

        }
      )
    );
  }

  guardarLocalStorage(token: string, menu: any) {
    localStorage.setItem("token", token);
    localStorage.setItem("menu", JSON.stringify(menu));
  }

  //PODRIA DAR ERROR FUNCION PARA GENERAR EL MENU AL RENOVAR EL TOKEN
  guardarLocalStorage2(menu: any) {
    localStorage.setItem("menu", JSON.stringify(menu));
  }
}
