import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UnidadService } from 'src/app/services/unidad.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-nuevo-unidad',
  templateUrl: './nuevo-unidad.component.html',
  styles: [
  ]
})
export class NuevoUnidadComponent implements OnInit {

  public unidadForm: FormGroup;

  constructor(private fb: FormBuilder,
    private unidadService: UnidadService,
    private router: Router) { }

  ngOnInit(){ this.crearFormulario();}

    //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
    empresa: string = localStorage.getItem('idemp');

    crearFormulario = () => {
      this.unidadForm = this.fb.group({
        idempresa: [this.empresa],
        idsucursal: ['SUC01'],
        idunidad: ['', Validators.required],
        nomunidad: ['', Validators.required],
        crepor: [localStorage.getItem('idcli')],
        modpor: [0],
        activo: [true],
      });
  
    }
  
    guardarUnidad() {
      this.unidadService.crearUnidad(this.unidadForm.value).subscribe(resp => {
        console.log(resp);
        Swal.fire('Creado','Nueva unidad creada correctamente','success');
        this.router.navigateByUrl('dashboard/unidades');
      }, (err) => {Swal.fire('Error','No se ha podido ingresar la unidad intente mas tarde','error');});
    }
  
    regresar() {
      this.router.navigateByUrl('dashboard/unidades');
    }
  


}
