import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UsuariosService } from 'src/app/services/usuarios.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-nuevo-usuarios',
  templateUrl: './nuevo-usuarios.component.html',
  styles: [
  ]
})
export class NuevoUsuariosComponent implements OnInit {

  public usuarioForm: FormGroup;

  constructor(private fb: FormBuilder,
    private usuariosService: UsuariosService,
    private router: Router) { }

  ngOnInit(){ this.crearFormulario();}

    //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
    empresa: string = localStorage.getItem('idemp');

    crearFormulario = () => {
      this.usuarioForm = this.fb.group({
        idempresa: [this.empresa],
        usuario: ['', Validators.required],
        password: ['', Validators.required],
        emailusuario: ['', Validators.required],
        idrol: [1],
        activo: [true],
        crepor: [localStorage.getItem('idcli')],
        modpor: [0],
        nomusuario: ['', Validators.required],
      });

    }

    guardarUsuario() {
      this.usuariosService.crearUsuario(this.usuarioForm.value).subscribe(resp => {
        console.log(resp);
        Swal.fire('Creado','Nuevo usuario creado correctamente','success');
        this.router.navigateByUrl('dashboard/usuarios');
      }, (err) => {Swal.fire('Error','No se ha podido ingresar el usuario intente mas tarde','error');});
    }

    regresar() {
      this.router.navigateByUrl('dashboard/usuarios');
    }


}