import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { FormBuilder, NgForm, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';
import { UsuarioModel } from '../../models/usuario.model';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: [
    './login.component.css'
  ]
})
export class LoginComponent{

  @Output() usuarioLogeado: EventEmitter<UsuarioModel> = new EventEmitter();

  usuario: UsuarioModel;

  public formSubmitted = false;

  public loginForm = this.fb.group({
    usuario: ['Soporte', Validators.required],
    password: ['password', Validators.required],
    remember: [false],
    token: true
  })

  constructor(private auth: AuthService, 
              private fb: FormBuilder) { }



  login(){
    this.auth.login(this.loginForm.value).subscribe(resp => {
      Swal.fire('Bienvenido','Ingreso exitoso','success');
      console.log(resp);
      this.usuarioLogeado = resp;
      window.location.href="index.html";
    }, (err)=>{
      Swal.fire('Error',err,'error');
    });
  }

}
