import { Component, OnInit } from '@angular/core';
import { Sucursal } from 'src/app/models/sucursal.model';
import Swal from 'sweetalert2';
import { SucursalService } from '../../services/sucursal.service';


@Component({
  selector: 'app-sucursal',
  templateUrl: './sucursal.component.html',
  styles: [
  ]
})

export class SucursalComponent implements OnInit {

  constructor(private sucursalServices: SucursalService) { }

  public sucursales: Sucursal[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");

  ngOnInit(): void {
    console.log("Iniciando");
    this.CargarSucursales();
  }

  CargarSucursales(){
    //console.log("object2");
    this.cargando = true;
    this.sucursalServices.DevolverSucursales(this.empresa).subscribe( ({parsucursal}) => {
      this.sucursales = parsucursal;
      this.cargando = false;
      console.log(parsucursal);
    });
  }

  Borrar(parsucursal: Sucursal){
    Swal.fire({
      title: 'Esta seguro de borrar la sucursal?',
      text: `Sucursal: ${parsucursal.idsucursal}`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then(result => {
      if(result.value){
              this.sucursalServices.BorrarSucursal(parsucursal.idsucursal).subscribe(resp => {
                this.CargarSucursales();
                Swal.fire('Sucursal Borrada',`${parsucursal.idsucursal} fue eliminado correctamente`,'success');
              });
      }
    })
  }
}
