import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Contplancuentas } from 'src/app/models/contplancuentas.model';
import { ContplancuentasService } from 'src/app/services/contplancuentas.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-actualizar-contplancuentas',
  templateUrl: './actualizar-contplancuentas.component.html',
  styles: [
  ]
})
export class ActualizarContplancuentasComponent implements OnInit {

  public plancuentasForm: FormGroup;
  public plancuentasSubido: FormGroup;
  private codigoCuenta: string = '';
  private contplancuentas: Contplancuentas;
  
  
  constructor(private fb: FormBuilder,
    private contplancuentasService: ContplancuentasService, 
    private router: Router,
    private activatedRoute: ActivatedRoute) 
    { }

  ngOnInit(){ this.crearFormulario();this.cargarPlancuentas();}

    //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
    empresa: string = localStorage.getItem('idemp');

    crearFormulario = () => {
      this.plancuentasForm = this.fb.group({
        idempresa: [this.empresa],
        idcuenta: ['', Validators.required],
        nomcuenta: ['', Validators.required],
        nivelcuenta: ['Nivel-1', Validators.required],
        cuentapadre: ['', Validators.required],
        crepor: [localStorage.getItem('idcli')],
        modpor: [0],
        activo: [true],
        tipocuenta: [true, Validators.required],
      });
  
    }
  
    cargarPlancuentas() {
      this.activatedRoute.params.subscribe(({ id }) => {
        this.codigoCuenta = id;
        console.log(this.codigoCuenta);
      });
      this.contplancuentasService.cargarPlancuentas(this.codigoCuenta).subscribe((resp) => {
         const {
          idempresa,
          idcuenta,
          nomcuenta,
          nivelcuenta,
          cuentapadre,
          crepor,
          activo,
          tipocuenta
        } = resp;
        const modpor = [localStorage.getItem('idcli')];
        this.contplancuentas = resp;
        this.plancuentasForm.setValue({
          idempresa,
          idcuenta,
          nomcuenta,
          nivelcuenta,
          cuentapadre,
          crepor,
          activo,
          tipocuenta,
          modpor,
        });
        return resp;
      });
      
    }
  
    actualizarPlancuentas() {
      this.plancuentasSubido = this.plancuentasForm.value;
      const datos = {
        ...this.plancuentasForm.value
      } 
      this.contplancuentasService
        .actualizarPlancuentas(
          datos.idcuenta,
          datos
        )
        .subscribe(
          (resp) => {
            console.log(resp);
            Swal.fire('Actualizado ', this.plancuentasForm.value['idcuenta'] + ' cuenta contable actualizada correctamente', 'success');
            this.router.navigateByUrl('dashboard/contplancuentas');
          },
          (err) => {
            Swal.fire(
              'Error',
              'No se ha podido actualizar la cuenta contable intente mas tarde',
              'error'
            );
            console.log(err);
          }
        );
    }
  
    regresar() {
      this.router.navigateByUrl('dashboard/contplancuentas');
    }

}
