import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ParserieService } from 'src/app/services/parserie.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-actualizar-serie',
  templateUrl: './actualizar-serie.component.html',
  styles: [
  ]
})
export class ActualizarSerieComponent implements OnInit {

  public serieForm: FormGroup;
  public serieSubida: FormGroup;
  private codigoSerie: string = '';
  

  constructor(private fb: FormBuilder,
    private parserieService: ParserieService,
    private router: Router,
    private activatedRoute: ActivatedRoute) { }

  ngOnInit(){ this.crearFormulario();
  this.cargarSerie();}

    //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
    empresa: string = localStorage.getItem('idemp');

    crearFormulario = () => {
      this.serieForm = this.fb.group({
        idempresa: [this.empresa],
        idsucursal: ['SUC01'],
        idserie: ['', Validators.required],
        tipserie: ['', Validators.required],
        ptoemision: ['', Validators.required],
        ptofacturacion: ['', Validators.required],
        secuencia: [0],
        crepor: [localStorage.getItem('idcli')],
        modpor: [0],
        activo: [true],
      });
  
    }

    cargarSerie() {
      this.activatedRoute.params.subscribe(({ id }) => {
        this.codigoSerie = id;
        console.log(this.codigoSerie);
      });
      this.parserieService.cargarSerie(this.codigoSerie).subscribe((resp) => {
        console.log(resp);
        const {
          idempresa,
          idsucursal,
          idserie,
          tipserie,
          ptoemision,
          ptofacturacion,
          secuencia,
          crepor,
          activo
        } = resp;
        const modpor = [localStorage.getItem('idcli')];
        this.serieForm.setValue({
          idempresa,
          idsucursal,
          idserie,
          tipserie,
          ptoemision,
          ptofacturacion,
          secuencia,
          crepor,
          modpor,
          activo,
        });
        return resp;
      });
    }
  
    actualizarSerie() {
      console.log('Guardar');
      this.serieSubida = this.serieForm.value;
      const datos = {
        ...this.serieForm.value
      } 
      console.log(datos);
      this.parserieService
        .actualizarSerie(
          datos.idserie,
          datos
        )
        .subscribe(
          (resp) => {
            console.log(resp);
            Swal.fire('Actualizado ', this.serieForm.value['idserie'] + ' serie actualizada correctamente', 'success');
            this.router.navigateByUrl('dashboard/serie');
          },
          (err) => {
            Swal.fire(
              'Error',
              'No se ha podido actualizar la serie intente mas tarde',
              'error'
            );
            console.log(err);
          }
        );
    }
  
    
    regresar() {
      this.router.navigateByUrl('dashboard/serie');
    }


}
