import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GLOBAL } from './global';
import { CargarProductos } from '../interfaces/cargar.interface';
import { Producto } from '../models/productos.model';
import { CrearProducto } from '../interfaces/crear-nuevoproduto.interface';

@Injectable({
  providedIn: 'root'
})
export class ProductosService {
  
  constructor(private http: HttpClient) {
    this.url=GLOBAL.url;
  }

  public productos: Producto[] = [];

  private url: string;

  //localhost:8010/api/productos/EMP01


  //Cargar producto por su id

  cargarProductoPorID(idproducto: string){
    const urlService = `${this.url}producto/${idproducto}`;
    return this.http.get<Producto>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Actualizar producto por su id

  actualizarProducto(producto: string, nuevoDatos: Producto){
    const urlService = `${this.url}producto/${producto}`;
    return this.http.put(urlService, nuevoDatos,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Funcion para cargar productos

  cargarProductos(empresa: string){
    const urlService = `${this.url}productos/${empresa}`;
    return this.http.get<CargarProductos>(urlService, {
      headers:{
        'Content-Type': 'Aplication/json',
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Funcion para borrar el producto seleccionado en la lista de productos

  BorrarProducto(idProducto: string){
    const urlService = `${this.url}productos/delete/${idProducto}`; 
    return this.http.delete(urlService,{
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

  //Servicio para crear un producto en la base
  crearProducto(formData: CrearProducto){
    const urlService = `${this.url}producto`;
    return this.http.post(urlService, formData, {
      headers:{
        'Authorization': localStorage.getItem('token')
      }
    });
  }

}
