import { Component, OnInit } from '@angular/core';
import { Contplancuentas } from 'src/app/models/contplancuentas.model';
import Swal from 'sweetalert2';
import { ContplancuentasService } from 'src/app/services/contplancuentas.service';


@Component({
  selector: 'app-contplancuentas',
  templateUrl: './contplancuentas.component.html',
  styles: [
  ]
})
export class ContplancuentasComponent implements OnInit {

  constructor(private contplancuentasServices: ContplancuentasService) { }

  public cuentas: Contplancuentas[] = [];
  public cargando: boolean = true;

  empresa: string = localStorage.getItem("idemp");

  ngOnInit(): void {
    console.log("Iniciando");
    this.CargarCuentas();
  }

  CargarCuentas(){
    //console.log("object2");
    this.cargando = true;
    this.contplancuentasServices.DevolverCuentas(this.empresa).subscribe( ({contplancuentas}) => {
      this.cuentas = contplancuentas;
      this.cargando = false;
      console.log(contplancuentas);
    });
  }

  Borrar(cuentas: Contplancuentas){
    Swal.fire({
      title: 'Esta seguro de borrar la cuenta?',
      text: `Cuenta: ${cuentas.idcuenta}`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then(result => {
      if(result.value){
              this.contplancuentasServices.BorrarCuentas(cuentas.idcuenta).subscribe(resp => {
                this.CargarCuentas();
                Swal.fire('Cuenta Borrada',`${cuentas.idcuenta} fue eliminado correctamente`,'success');
              });
      }
    })
  }

}
