import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CuentaContable } from 'src/app/models/conplancuentas';
import { CuentasService } from 'src/app/services/cuentas.service';
import { ImpuestosService } from 'src/app/services/impuestos.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-actualizar-impuestos',
  templateUrl: './actualizar-impuestos.component.html',
  styles: [
  ]
})
export class ActualizarImpuestosComponent implements OnInit {

  public impuestoForm: FormGroup;
  public impuestoSubido: FormGroup;
  public cuentasContables: CuentaContable[] = [];
  private codigoImpuesto: string = '';

  constructor(private fb: FormBuilder,
    private cuentasService: CuentasService,
    private impuestosService: ImpuestosService,
    private router: Router,
    private activatedRoute: ActivatedRoute) 
    { 
      this.CargarCuentas(this.empresa);
    }

    CargarCuentas(idempres: string){
      this.cuentasService.CargarCuentas(this.empresa).subscribe(({contplancuentas})=>{
        this.cuentasContables = contplancuentas;
      });
    }

  ngOnInit(){ this.crearFormulario();
  this.cargarImpuesto();}

    //CORREGIR PARA HACER LA LLAMADA DESDE EL SERVICIO
    empresa: string = localStorage.getItem('idemp');

    crearFormulario = () => {
      this.impuestoForm = this.fb.group({
        idempresa: [this.empresa],
        idimpuesto: ['', Validators.required],
        fechaini: ['', Validators.required],
        fechafin: ['', Validators.required],
        nomimpuesto: ['', Validators.required],
        idcuenta: ['', Validators.required],
        crepor: [localStorage.getItem('idcli')],
        modpor: [0],
        idsucursal: ['SUC01'],
        activo: [true],
        porcimpuesto: [0],
        codigoSRI: ['', Validators.required],
        codporcentajeSRI: ['', Validators.required],
      });
  
    }

    cargarImpuesto() {
      this.activatedRoute.params.subscribe(({ id }) => {
        this.codigoImpuesto = id;
        console.log(this.codigoImpuesto);
      });
      this.impuestosService.cargarImpuesto(this.codigoImpuesto).subscribe((resp) => {
        console.log(resp);
        const {
          idempresa,
          idsucursal,
          idimpuesto,
          fechaini,
          fechafin,
          nomimpuesto, 
          idcuenta,
          crepor,
          activo,
          porcimpuesto,
          codigoSRI,
          codporcentajeSRI
        } = resp;
        const modpor = [localStorage.getItem('idcli')];
        this.impuestoForm.setValue({
          idempresa,
          idsucursal,
          idimpuesto,
          fechaini,
          fechafin,
          nomimpuesto, 
          idcuenta,
          crepor,
          modpor,
          activo,
          porcimpuesto,
          codigoSRI,
          codporcentajeSRI
        });
        return resp;
      });
    }
  
  
    actualizarImpuesto() {
      console.log('Guardar');
      this.impuestoSubido = this.impuestoForm.value;
      const datos = {
        ...this.impuestoForm.value
      } 
      console.log(datos);
      this.impuestosService
        .actualizarImpuesto(
          datos.idimpuesto,
          datos
        )
        .subscribe(
          (resp) => {
            console.log(resp);
            Swal.fire('Actualizado ', this.impuestoForm.value['idimpuesto'] + ' impuesto actualizada correctamente', 'success');
            this.router.navigateByUrl('dashboard/impuesto');
          },
          (err) => {
            Swal.fire(
              'Error',
              'No se ha podido actualizar el impuesto intente mas tarde',
              'error'
            );
            console.log(err);
          }
        );
    }
  
  
    regresar() {
      this.router.navigateByUrl('dashboard/impuesto');
    }

}
